<template lang="html">

</template>

<script>

import isFunction from 'lodash/isFunction'
import isEmpty from 'ramda/src/isEmpty'
import isNumber from 'lodash/isNumber'
import isArray from 'lodash/isArray'
import isString from 'lodash/isString'
import isBoolean from 'lodash/isBoolean'
import contains from 'ramda/src/contains'

export default {
    name: 'datatable-column',
    props: {
        // 列的标题
        title: {
            type: String,
            default: ''
        },
        // 列的属性名
        field: {
            type: String,
            default: 'normal'
        },
        // 列的宽度
        width: {
            type: [Number, String],
            default: ''
        },
        // 列的最小宽度
        minWidth: {
            type: [Number, String],
            default: 'none'
        },
        // 是否排序
        sortable: {
            type: Boolean,
            default: false
        },
        // 排序的方法
        sorter: {
            type: Function,
            default: function(a, b) {
                if(a.toString() < b.toString()) {
                    return 1;
                }else if(a.toString() > b.toString()) {
                    return -1;
                }else {
                    return 0;
                }
            }
        },
        // 筛选的条件值
        filterValue: {
            default: ''
        },
        // 筛选的方法
        filter: {
            type: Function,
            default: function(filterVal, val, item) {
                if(isEmpty(filterVal)) return true;
                if(isBoolean(filterVal)||isNumber(filterVal)) {
                    return val === filterVal;
                }
                if(isArray(filterVal)) {
                    return contains(val, filterVal);
                }
                if(isString(filterVal)) {
                    return val.toString().indexOf(filterVal) > -1;
                }
            }
        },
        // 列的固定 可选 left right 默认 none
        fixed: {
            type: String,
            default: 'none'
        }
    },
    data() {
        return {
            // 单元格的类型，可选 index, checkbox, radio, normal
            type: 'normal',
            cellRender: function(props) {
                return props.value.toString();
            },
            sortState: 'none'
        }
    },
    methods: {
        changeSortState() {
            var states = ['none', 'desc', 'asc'],
                index = states.indexOf(this.sortState);
            this.sortState = states[(index+1)%3];
        },
        clearSortState() {
            this.sortState = 'none';
        }
    },
    mounted() {
        // 判断是否需要获取单元格模板
        if(isFunction(this.$scopedSlots.default)) {
            this.cellRender = this.$scopedSlots.default;
        }
    }
}
</script>



