<template lang="html">
  <div>
      <ul class="sf-crumbs">
          <li><a href="#">商户列表</a></li>
      </ul>
      <div class="sf-panel sf-mv10 sf-mh15 sf-pv5">
          <sf-condition-box :height="39" foldable>
              <form fluid>
                  <form-group>
                      <label>商户编号</label>
                      <input type="text" class="sf-input" v-model="filters.id" @keydown.enter="updateList(true)">
                  </form-group>
                  <form-group>
                      <label>商户类型</label>
                      <sf-select v-model="filters.type" @input="updateList(true)">
                          <sf-option  :value="''" title="无"></sf-option>
                          <sf-option v-for="type in mTypes" :value="type" :key="type"></sf-option>
                      </sf-select>
                  </form-group>
                  <form-group>
                      <label>注册号</label>
                      <input type="text" class="sf-input" v-model="filters.number" @keydown.enter="updateList(true)">
                  </form-group>
                  <form-group>
                      <label>商户名称</label>
                      <input type="text" class="sf-input" v-model="filters.name" @keydown.enter="updateList(true)"> 
                  </form-group>
              </form>
              <button slot="corner" type="button" class="sf-button sf-sm" @click="updateList(true)"><i class="fa fa-search"></i> 查询</button>
          </sf-condition-box>
      </div>
      <div class="sf-panel sf-mv10 sf-mh15 sf-pv10">
          <ul class="sf-toobar">
              <li @click="$router.push('/form')"><i class="fa fa-plus sf-color-primary"></i> 新增</li>
              <li><i class="fa fa-download sf-color-primary"></i> 导出</li>
          </ul>
      </div>
      <div class="sf-panel sf-pb15 sf-pt5 sf-mv10 sf-mh15" v-spinner="loading">
          <table class="sf-table">
              <tr>
                  <th class="sf-tc">序号</th>
                  <th>商户编号</th>
                  <th>注册号</th>
                  <th>商户名称</th>
                  <th>商户类型</th>
                  <th>操作</th>
              </tr>
              <tr v-for="(item, index) in list" :key="item.id">
                  <td class="sf-tc sf-w40" v-text="index+(paging.page-1)*paging.limit+1"></td>
                  <td>
                      <router-link :to="{path:'/detail', query:{id: item.id}}">
                          <span v-text="item.id"></span>
                      </router-link>
                  </td>
                  <td v-text="item.number"></td>
                  <td v-text="item.name"></td>
                  <td v-text="item.type"></td>
                  <td>
                      <button class="sf-button sf-primary-o sf-sm" @click="$router.push({path:'/form', query: {id:item.id}})">编辑</button>
                      <button class="sf-button sf-danger-o sf-sm" @click="remove(item)">删除</button>
                  </td>
              </tr>
          </table>
          <sf-pagination :total="paging.total" :page-size="paging.limit" class="sf-mt10" v-model="paging.page" @input="updateList()"></sf-pagination>
      </div>
      <!--<panel-alert :status="status"></panel-alert>-->

  </div>
</template>

<script>
// TODO: 条件和分页状态保存功能 ✅
// TODO: 公共变量共享 ✅
// TODO: 权限控制

import store from '../store'
import axios from '../utils/axios'
export default {
    name: 'List-keep-alive',
    data() {
        return {
            loading: false, 
            // 列表数据
            list: [],
            // 过滤条件
            filters: {
                id: '',
                type: '',
                number: '',
                name: ''
            },
            // 分页条件
            paging: {
                page: 1, // 分页页码
                total: 100, // 数据总数
                limit: 50, // 每页的数据量
                total: 0
            },
            // 暂存查询条件
            query: { }
        }
    },
    computed: {
        mTypes() {
            return store.mTypes;
        }
    },
    activated() {
        this.updateList();
    },
    created() {
        this.updateList();
    },
    mounted() {},
    methods: {
        async updateList(filter) {
            this.loading = false;
            if(filter) {
                this.query = Object.assign({
                    _page: this.paging.page,
                    _limit: this.paging.limit
                }, this.filters)
            }else {
                this.query = Object.assign(this.query, {
                    _page: this.paging.page,
                    _limit: this.paging.limit
                })
            }
            
            try {
                var res = await axios('/customers', {params: this.query})
                this.paging.total = res.headers['X-Total-Count'];
                this.list = res.data;
            } catch (e) {
                alert('数据请求失败')
            } finally {
                this.loading = false;
            }
        },
        async remove(item) {
            await axios.delete(`/customers/${item.id}`)
            this.updateList()
        }
    }
}
</script>

<style lang="css">
</style>



