<template lang="html">
    <div>
        <ul class="sf-crumbs">
            <li>日报</li>
            <li><a href="#/daily-statis">日报统计</a></li>
            <li>用户新增</li>
        </ul>
        <div class="sf-panel sf-pb20 sf-mv10 sf-mh15">
            <div class="sf-panel-header" >
                用户基本信息
            </div>
            <sf-validator ref="validator">
                <form>
                    <form-group>
                        <label>用户id</label>
                        <sf-validator-message :value="form.userId" :rules="{required: '用户id必须填写'}">
                            <input type="text" class="sf-input" v-model="form.userId">
                        </sf-validator-message>
                    </form-group>
                    <form-group>
                        <label>姓名</label>
                        <input type="text" class="sf-input" v-model="form.userName">
                    </form-group>
                    <form-group>
                        <label>性别</label>
                        <sf-select v-model="form.sex">
                            <sf-option :value="''" title="无"></sf-option>
                            <sf-option value="female" key="female"></sf-option>
                            <sf-option value="male" key="male"></sf-option>
                        </sf-select>
                    </form-group>
                    <form-group>
                        <label>地址</label>
                        <input type="text" class="sf-input sf-w300" v-model="form.address">
                    </form-group>
                </form>
            </sf-validator>

            <p class="sf-tc sf-mt40">
                <button type="button" class="sf-button" onclick="history.go(-1)">返回</button>
                <button type="button" class="sf-button" @click="submit">提交</button>
            </p>
        </div>
    </div>
</template>

<script>
    import store from '../../store'
    import axios from '../../utils/axios'
    export default {
        name: 'Form',
        data() {
            return {
                form: {
                    userId: '',
                    userName: '',
                    sex: '',
                    address: '',
                }
            }
        },
        methods: {
            async submit() {
                // 强制对表单进行校验，返回校验的结果
                var r = await this.$refs.validator.forceValid();
                if(!r) return;
                $.ajax({
                    url: 'http://localhost:8011/milestone/admin/addUser',
                    type: 'post',
                    contentType: 'application/json',
                    data: JSON.stringify(this.form)
                }).then(function(res){
                    if (res.code == 0) {
                        this.$toast('添加成功');
                        this.$router.push('/daily-statis');
                    } else {
                        this.$toast('添加失败', 'danger');
                    }
                }.bind(this));

            }
        }
    }
</script>

<style lang="css">
</style>



