<template lang="html">
    <div class="sf-form-group">
        <sfi-row>
            <sfi-col :span="labelSpan">
                <label class="sf-form-group__label">{{label}}<span v-if="isRequired" class="sf-form-group__star"> *</span></label>
            </sfi-col>
            <sfi-col :span="controlSpan">
                <slot></slot>
            </sfi-col>
            <sfi-col :span="1" v-if="hasDescription">
                <span class="sf-form-group__description">{{description}}</span>
            </sfi-col>
        </sfi-row>
    </div>
</template>

<script>
    import row from './row'
    import col from './col'
    export default {
        name: 'form-group',
        props:{
            label:{
                type: String,
                default: ''
            },
            labelSpan:{
                type: [String,Number],
                default: 0
            },
            controlSpan:{
                type: [String,Number],
                default: 1
            },
            description: {
                type: String,
                default: ''
            },
            isRequired: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            hasDescription() {
                if(this.description === '') return false;
                return true;
            }
        },
        components: {
            'sfi-col': col,
            'sfi-row': row
        }
    }
</script>



