
<script>
import expand from '../expand/expand.vue'
import inputBox from '../input-box/input-box.vue'
import inputCell from '../input-box/input-cell.vue'
import uniq from 'ramda/src/uniq'
import append from 'ramda/src/append'
import dropLast from 'ramda/src/dropLast'
import remove from 'ramda/src/remove'
import isArray from 'lodash/isArray'
import debounce from 'lodash/debounce'

export default {
    name: 'select',
    props: {
        // 表单元素值
        value: {
            default: ''
        },
        // 是否多选
        multiple: {
            type: Boolean,
            default: false
        },
        // 出现搜索框的阈值
        searchThreshold: {
            type: Number,
            default: 10
        },
        // 多选时候限制选择的最大数量
        limit: {
            type: Number,
            default: Infinity
        },
        // 多选允许显示的最大行数
        maxLine: {
            type: Number,
            default: 0
        },
        remote: {
            type: Boolean,
            default: false
        },
        readonly: {
            type: Boolean,
            default: false
        },
        placeholder: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            // 是否显示expand
            show: false,
            // 搜索关键字
            searchText: '',
            // options
            options: [],
            // 当前hover 的元素
            hoverOption: null,
            // 是否 focus
            isFocus: false,
            // 被选中的选项
            optionsSelected: null
        }
    },
    computed: {
        // input的最大高度
        maxHeight() {
            if(this.maxLine > 0) {
                return 6+22*this.maxLine;
            }else {
                return 'none';
            }
        },
        // 搜索的结果
        optionsSearched() {
            // 如果是远程模式，则不对选项进行筛选，筛选工作放到后端进行
            if(this.remote) {
                return this.options;
            }
            return this.options.filter(this.search);
        },
        // 是否可以搜索
        searchable() {
            if(this.remote) {
                return true;
            }
            return this.options.length > this.searchThreshold;
        },
    },
    created() {
        if(this.multiple) {
            this.optionsSelected = []
        }
    },
    watch: {
        optionsSearched() {
            this.hoverOption = this.optionsSearched[0];
        },
        searchText(val) {
            if(this.show) {
                // this.$emit('search', val);
                this.emitSearch();
            }
        },
        value() {
            // if(this.remote) return;
            this.initValue();
        },
        options() {
            // if(this.remote) return;
            this.initValue();
        }
    },
    mounted() {
        // window.addEventListener('click', this.windowClickHandler);
        // this.$el.addEventListener('click', this.selfClickHandler);
        // this.$refs.expand.$box.addEventListener('click', this.selfClickHandler);
        window.addEventListener('click', this.otherClickHandler)
        this.$refs.expand.$box.querySelector('.sf-select__options').addEventListener('scroll', this.scrollHandler)
        this.initValue();

        this.initOptions();

    },
    beforeDestroy() {
        clearTimeout(this.f);
        // window.removeEventListener('click', this.windowClickHandler);
        // this.$el.removeEventListener('click', this.selfClickHandler);
        // this.$refs.expand.$box.removeEventListener('click', this.selfClickHandler);
        window.removeEventListener('click', this.otherClickHandler)
        this.$refs.expand.$box.querySelector('.sf-select__options').removeEventListener('scroll', this.scrollHandler)
    },
    methods: {
        emitSearch: debounce(function(){
            this.$emit('search', this.searchText);
        }, 200),
        focus() {
            setTimeout(()=> {
                this.expand();
            })
        },
        contains(elm) {
            if(this.$el.contains(elm)) {
                return true;
            }
            if(this.$refs.expand.$box.contains(elm)) {
                return true;
            }
            return false;
        },
        // 滚动处理函数
        scrollHandler() {
            if(!this.remote) return ;
            // 判断是否滚动到底部
            var $options = this.$refs.options;
            var offset = 100
            if($options.scrollTop >=  ($options.scrollHeight - $options.clientHeight) ) {
                this.$emit('bottom', this.searchText)
            }
        },
        initValue() {
            // 根据值初始化显示的内容
            if(this.multiple) {
                if(!this.remote) {
                    this.optionsSelected = [];
                }
                this.options.forEach(option => {
                    
                    if(this.value.indexOf(option.optionValue) > -1 ) {
                    // if(this.value.filter(item=>item==option.optionValue) > 0) {
                        if(this.optionsSelected.filter(item=>item.optionValue===option.optionValue).length >0) return;
                        this.optionsSelected.push({
                            optionValue: option.optionValue,
                            optionTitle: option.optionTitle
                        })
                    }
                })
            }else {
                // XXX: 考虑是否需要提供空值选项
                // XXX: remote 模式下如果在组件外部改变值，会出现bug，以后考虑remote 模式下单独存储被选中的值
                if(!this.remote) {
                    this.optionsSelected = {
                        optionValue: this.value,
                        optionTitle: this.placeholder
                    }
                }
                this.options.forEach((option) => {
                    if(this.value === option.optionValue) {
                    // if(this.value == option.optionValue) {
                        this.optionsSelected = {
                            optionValue: option.optionValue,
                            optionTitle: option.optionTitle
                        }
                    }
                })
            }
        },
        // 初始化选项的状态
        initOptions() {
            this.searchText = '';
            this.hoverOption = this.optionsSearched[0];
            this.f = setTimeout(() => {
                this.$refs.options.scrollTop = 0;
            })
        },
        windowClickHandler(e) {
            if (e.from !== 'select') {
                this.show = false;
            }
        },
        selfClickHandler(e) {
            e.from = 'select';
        },
        otherClickHandler(event) {
            if(this.$el.contains(event.target)) {
                return ;
            }
            if(this.$refs.expand.$box.contains(event.target)) {
                return;
            }
            this.show = false;
            this.searchText = '';
        },
        // 展开选项
        expand() {
            if(this.readonly) { return }
            this.show = true;
            this.initOptions();
            this.$emit('search', this.searchText);
        },
        // 清除选项
        clear() {
            // 在单选情况下
            this.$emit('input', '');
            this.searchText = '';
        },
        // 取消选择某个选项
        unselect(option) {
            if(this.multiple) {
                this.value.splice(this.value.indexOf(option.optionValue), 1);
                this.optionsSelected.splice(this.value.indexOf(option.optionValue),1);
                // 再次聚焦
                setTimeout(()=> {
                    this.$refs.inputBox.focus();
                })
            }
        },
        // 选中某个选项
        select(option) {
            if (!this.multiple) {
                // 如果是单选模式
                this.$emit('input', option.optionValue);
                this.show = false;
                this.$refs.inputBox.blur();
                this.searchText = '';
                // 记录被选中的值
                this.optionsSelected = {
                    optionValue: option.optionValue,
                    optionTitle: option.optionTitle
                }
            } else {
                // 如果是多选模式
                // 当前选择的数量是否已经超过限制, 则无法选择
                if(this.limit <= this.optionsSelected.length) {
                    setTimeout(()=> {
                        this.$refs.inputBox.focus();
                    })
                    return;
                }
                var value = append(option.optionValue, this.value);
                this.$emit('input', value)
                if(!isArray(this.optionsSelected)) this.optionsSelected = [];
                this.optionsSelected.push({
                    optionValue: option.optionValue,
                    optionTitle: option.optionTitle
                })
                // 如果是多选框，则再次
                setTimeout(()=> {
                    this.$refs.inputBox.focus();
                })
            }
        },
        // 定位到某个选项
        hover(option) {
            this.hoverOption = option;
        },
        // 定位到下一个选项
        hoverNext() {
            var current = this.optionsSearched.indexOf(this.hoverOption);
            if (current + 1 >= this.optionsSearched.length) {
                this.hoverOption = this.optionsSearched[0];
            } else {
                this.hoverOption = this.optionsSearched[current + 1];
            }
        },
        // 定位到上一个选项
        hoverPrev() {
            var current = this.optionsSearched.indexOf(this.hoverOption);
            if (current <= 0) {
                this.hoverOption = this.optionsSearched[this.optionsSearched.length - 1];
            } else {
                this.hoverOption = this.optionsSearched[current - 1];
            }
        },
        // 对内容进行筛选
        search(option, index) {
            if (this.searchText === 'true') return true;
            return option.optionTitle.toString().indexOf(this.searchText) >= 0;
        },
        // 如果 options 越过了视口，则重写定位 scroll
        scrollFix() {
            var $options = this.$refs.options,
                $target = $options.querySelector('.sf-select__option--hover');
            // 如果上边界超出了
            if ($options.scrollTop > $target.offsetTop) {
                $options.scrollTop = $target.offsetTop;
                return;
            }
            // 如果下边界超出了
            if ($options.scrollTop < $target.offsetTop - $options.clientHeight + $target.offsetHeight) {
                $options.scrollTop = $target.offsetTop - $options.clientHeight + $target.offsetHeight
                return;
            }
        },
        // 判断某个选项是否已经被选中
        isOptionSelected(option) {
            if(this.multiple) {
                // 多选模式下
                if(this.value.indexOf(option.optionValue) >= 0) return true;
                // if(this.value.filter(item=>item==option.optionValue) > 0) return true;
                else return false;
            }else {
                if(this.value === option.optionValue) return true;
                // if(this.value == option.optionValue) return true;
                else return false;
            }
        }
    },
    components: {
        expand,
        'input-box': inputBox,
        'input-cell': inputCell
    },
    render(h) {
        return h('div', {
            class: ['sf-select', this.multiple?'sf-select--multiple':'sf-select--single', this.readonly?'sf-select--readonly':'']
        }, [
            h('expand', {
                props: {
                    show: this.show,
                    maxHeight: 1000
                },
                ref: 'expand'
            }, [
                // 多选框
                this.multiple?h('input-box', {
                    style: {
                        'max-height': `${this.maxHeight}px`,
                        'overflow': 'auto'
                    },
                    props: {
                        value: this.searchText,
                        editable: this.searchable
                    },
                    ref:"inputBox",
                    on: {
                        focus: this.expand,
                        input: (val) => {
                            this.searchText = val;
                            if(!this.show) {
                                this.expand();
                            }
                        },
                        down: () => {
                            if (this.show) {
                                this.hoverNext();
                                setTimeout(() => {
                                    this.scrollFix();
                                })

                            } else {
                                this.expand();
                            }
                        },
                        up: () => {
                            if (this.show) {
                                this.hoverPrev();
                                setTimeout(() => {
                                    this.scrollFix();
                                })
                            } else {
                                this.expand();
                            }
                        },
                        enter: () => {
                            if (this.show) {
                                if(this.value.indexOf(this.hoverOption.optionValue) >=0) {
                                    this.unselect(this.hoverOption);
                                }else {
                                    this.select(this.hoverOption);
                                }
                            } else {
                                this.expand();
                            }
                            this.searchText = '';
                        },
                        blur: () => {
                            // this.show = false;
                            this.searchText = '';
                        },
                        tab: event=>{
                            setTimeout(()=>{
                                if(!this.$refs.inputBox.$el.contains(document.activeElement)) {
                                    this.show = false;
                                }
                            })
                        },
                        remove: event=> {
                            this.$emit('input', dropLast(1, this.value))
                            this.optionsSelected = dropLast(1, this.optionsSelected)
                        },
                        esc: event=>{
                            this.show = false;
                        }
                    }
                }, [
                    this.optionsSelected.length===0?h('span', [this.placeholder]):'',
                    ...this.optionsSelected.map(option => h('transition', [
                        h('input-cell', {
                            on: {
                                remove: event=>{
                                    var index = this.value.indexOf(option.optionValue);
                                    this.$emit('input', remove(index, 1, this.value));
                                    this.optionsSelected.splice(index, 1);
                                }
                            }
                        }, [option?option.optionTitle:''])
                    ]))
                // 单选框
                ]):h('input-box', {
                    props: {
                        value: this.searchText,
                        editable: this.searchable
                    },
                    ref:"inputBox",
                    on: {
                        focus: this.expand,
                        input: (val) => {
                            this.searchText = val;
                            if(!this.show) {
                                this.expand();
                            }
                        },
                        down: () => {
                            if (this.show) {
                                this.hoverNext();
                                setTimeout(() => {
                                    this.scrollFix();
                                })

                            } else {
                                this.expand();
                            }
                        },
                        up: () => {
                            if (this.show) {
                                this.hoverPrev();
                                setTimeout(() => {
                                    this.scrollFix();
                                })
                            } else {
                                this.expand();
                            }
                        },
                        enter: () => {
                            if (this.show) {
                                this.select(this.hoverOption);
                            } else {
                                this.expand();
                            }
                        },
                        blur: () => {
                            // this.show = false;
                            this.searchText = '';
                        },
                        esc: event=>{
                            this.show = false;
                        },
                        tab: event=>{
                            setTimeout(()=>{
                                if(!this.$refs.inputBox.$el.contains(document.activeElement)) {
                                    this.show = false;
                                }
                            })
                        }
                    }
                },[
                    h('span', {
                        directives: [{
                            name: 'show',
                            value: this.$refs.inputBox?(!this.$refs.inputBox.isFocus||this.readonly):true
                        }]
                    }, [this.optionsSelected?this.optionsSelected.optionTitle:''])
                ]),
                h('div', {
                    slot: 'content'
                }, [
                    h('ul', {
                        class: ['sf-select__options'],
                        ref: 'options'
                    }, this.optionsSearched.length<=0?[
                        h('li', {
                            class: ['sf-select__notfound']
                        },['没有结果'])
                    ]:this.optionsSearched.map(option => {
                        return h('li', {
                            class: {
                                'sf-select__option': true,
                                'sf-select__option--actived': this.isOptionSelected(option),
                                    'sf-select__option--hover': option === this.hoverOption
                            },
                            on: {
                                mousedown: event => {
                                    if(this.multiple) {
                                        if(this.value.indexOf(option.optionValue) >=0) {
                                            this.unselect(option);
                                        }else {
                                            this.select(option);
                                        }
                                    }else {
                                        this.select(option);
                                    }
                                },
                                mouseenter: event => {
                                    this.hover(option)
                                }
                            }
                        }, option.$slots.default||option.optionTitle);
                    }))
                ])
            ]),
            h('div', {
                style: {
                    display: 'none'
                }
            }, this.$slots.default)
        ])
    }
}
</script>



