<template lang="html">
    <div
        class="sf-alert"
        v-if="activeAlert !== null"
    >
        <div
            class="sf-alert__content"
            :style="{'width': width+'px'}"
            v-show="activeAlert == alert"
            v-for="alert in alerts.concat([activeAlert])"
            v-if="alert !== null"
        >
            <div class="sf-alert__body">
                <div class="sf-alert__message" v-html="alert.message"></div>
            </div>
            <div class="sf-alert__footer">
                <button type="button" @click="close" class="sf-alert__sure">确定</button>
            </div>
        </div>
    </div>
</template>

<script>
    import isFunction from 'lodash/isFunction'
    export default {
        name:'alert',
        data: function () {
            return {
                // 提示框的宽度
                width: 400,
                // 所有当前需要呈现的提示框
                alerts: [],
                // 目前显示的提示框
                activeAlert: null
            }
        },
        props: {
            icon: {
                type: String,
                default: ''
            }
        },
        methods: {
            alert({message, callback}){
                return  new Promise((resolve)=>{
                    // 处理换行符
                    this.alerts.unshift({message, callback, resolve});
                    this.ask();
                });
            },
            close() {
                if(this.activeAlert === null) return ;

                if(isFunction(this.activeAlert.callback)) {
                    this.activeAlert.callback();
                }
                this.activeAlert.resolve();
                this.activeAlert = null;
                this.ask();
            },
            ask() {
                if(this.activeAlert === null) {
                    if(this.alerts.length > 0) {
                        this.activeAlert = this.alerts.pop();
                    }
                }
            }
        }
    }
</script>



