<template lang="html">
    <div class="">
        <sf-panel-box class="sf-mv10 sf-mh15">
            <a href="http://www.kancloud.cn/wangfupeng/wangeditor2/113963">wang editor 文档</a>
        </sf-panel-box>
        <sf-panel-box title="值的绑定" class="sf-mv10 sf-mh15">
            <div ref="editor" style="height: 500px;"></div>
            <br>
            <br>
            <h6>提交的值：</h6>
            <p>{{value}}</p>
            <h6>显示的内容</h6>
            <div v-html="value"></div>
        </sf-panel-box>
    </div>
</template>

<script>
    import WangEditor from 'wangeditor'
    import jQuery from 'jQuery'
    window.$ = window.jQuery = jQuery;
    export default {
        data() {
            return {
                value: '<p>askldjfaiealskj</p>'
            }
        },
        mounted() {
            this.initEditor();
        },
        methods: {
            initEditor() {
                // 创建编辑器
                let editor = new WangEditor(this.$refs.editor);
                editor.create();
                // 将编辑器中的变动同步到value
                editor.onchange = () => {
                    this.value = editor.$txt.html();
                }
                // 用value来初始化编辑器中的内容
                if(!!this.value) {
                    editor.$txt.html(this.value)
                }
            }
        }
    }
</script>

<style lang="less">
     @import '../../node_modules/wangeditor/dist/css/wangEditor.less';
</style>



