import Popper from 'popper.js'
import validator from '../validator/validator.vue'
import validatorMessage from '../validator/validator-message.vue'
export default {
    name: 'editiny',
    props: {
        value: {
            default: ''
        },
        rules: {
            type: Object,
            default() {
                return {}
            }
        }
    },
    data() {
        return {
            editing: false,
            valueBuffer: ''
        }
    },
    mounted() {
        this.popper = new Popper(this.$el, this.$refs.editor, {
            placement: 'left',
            modifiers: {
                inner: {
                    enabled: true
                }
            }
        })
        window.addEventListener('click', this.otherClickHandler)

        // 获取编辑器
        if(this.$slots.default&&this.$slots.default[0]) {
            this.$editor = this.$slots.default[0].child||this.$slots.default[0].elm;
        }

        this.valueBuffer = this.value;
    },
    updated() {
        this.popper.update();
    },
    beforeDestroy() {
        window.removeEventListener('click', this.otherClickHandler)
    },
    methods: {
        enable() {
            this.$emit('beforeEdit');
            this.editing = true;
            this.$nextTick(()=>{
                this.$editor.focus();
            })
            // 暂时存储值
            this.valueBuffer = this.value;
        },
        disable() {
            this.editing = false;
        },
        async save() {
            if(await this.$refs.validator.forceValid()) {
                this.$emit('save', this.value);
                this.disable();
            }
        },        
        otherClickHandler(event) {
            if(!this.editing) return ;
            if(this.$el.contains(event.target)||this.$editor.contains(event.target)) {
                return ;
            }
            if(this.value === this.valueBuffer) {
                this.disable();
            }else {
                this.save();
            }
            

            // 将存储的值归还
            // this.$emit('input', this.valueBuffer);
        },
        keyupHandler() {
            if (event.keyCode === 27) {
                // 将存储的值归还
                this.$emit('input', this.valueBuffer);
                this.disable();
            }else if (event.keyCode === 13) {
                this.save();
            }
        }
    },
    components: {
        validator,
        validatorMessage
    },
    render(h) {
        return <span class="sf-editiny" x-editing={ this.editing } onKeyup={ this.keyupHandler }>
            <span ref="content" class="sf-editiny__content" onClick={ this.enable }>
                { this.$slots.content||this.value } 
            </span>
            <i class="fa fa-pencil sf-editiny__edit" onClick={ this.enable }></i>
            <span ref="editor" class="sf-editiny__editor" v-show={ this.editing }>
                <validator ref="validator" style={{display: 'inline-block'}}>
                    <validator-message value={ this.value } rules={ this.rules }>
                        { this.$slots.default } 
                    </validator-message>
                </validator>
                <i class="fa fa-check sf-editiny__save" onClick={ this.save }></i>
            </span>
        </span>
    }
}


