<template lang="html">
    <span class="sf-radio"><input
            class="sf-radio__input"
            type="radio"
            :value="option"
            v-model="model"
        ><span class="sf-radio__fake"></span><slot></slot>
    </span>
</template>

<script>
    export default {
        name: 'radio',
        props: {
            option: '',
            value: ''
        },
        computed: {
            model: {
                set(value) {
                    this.$emit('input', value);
                },
                get() {
                    return this.value;
                }
            }
        }
    }
</script>



