<template lang="html">
    <li :class="['sf-menuh__item', 'sf-menuh__item--l'+level]">
        <sf-expand
            class="sf-menuh__expand"
            :show="showSub"
            :width="150"
            v-if="hasSub"
            ref="expand"
            placement="bottom-end"
            @click="expandClickHandler"
        >
            <div
                :class="['sf-menuh__title', 'sf-menuh__title--l'+level]"
                @click="toggle"
            >
                <slot></slot>
                <i class="fa fa-caret-down"></i>
            </div>
            <div slot="content">
                <div>
                    <slot name="submenu"></slot>
                </div>
            </div>
        </sf-expand>
        <div
            :class="['sf-menuh__title', 'sf-menuh__title--l'+level]"
            @click="$emit('click')"
            v-else
        >
            <slot></slot>
        </div>
    </li>
</template>

<script>
import expand from '../expand/expand.vue'
import isArray from 'lodash/isArray'
export default {
    name: 'menuh-item',
    data() {
        return {
            level: 0,
            showSub: false
        }
    },
    computed: {
        hasSub() {
            return !!this.$slots.submenu;
        }
    },
    methods: {
        addLevel() {
            this.level++;
            if(!isArray(this.$slots.submenu)) return;
            this.$slots.submenu.forEach(submenu=>{
                submenu.child.addLevel();
            })
        },
        // 切换是否显示子菜单
        toggle() {
            this.showSub = !this.showSub;
        },
        windowClickHandler(e) {
            if(this.isExpandClick) {
                this.isExpandClick = false;
            }else {
                this.showSub = false;
            }
        },
        expandClickHandler(e) {
            this.isExpandClick = true;
        }
    },
    mounted() {
        window.addEventListener('click', this.windowClickHandler);
    },
    beforeDestroy() {
        window.removeEventListener('click', this.windowClickHandler);
    },
    components: {
        'sf-expand': expand
    }
}
</script>



