<template lang="html">
    <div class="fs-expand-box" :style="style">
        <slot></slot>
    </div>
</template>

<script>
export default {
    name: 'box',
    props: {
        maxHeight: {
            type: Number,
            default: 350
        },
        width: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            style: {
                maxHeight: this.maxHeight + 'px',
                width: this.width + 'px'
            }
        }
    }
}
</script>



