<template lang="html">
    <span class="sf-input-cell">
        <slot></slot>
        <span class="sf-input-cell__close" @click="$emit('remove'),refocus()">×</span>
    </span>
</template>

<script>
export default {
    name: 'input-cell',
    props: {

    },
    methods: {
        // 重新 focus
        refocus() {
            this.$parent.focus(true);
        }
    }
}
</script>



