<template lang="html">
    <div>
        <ul class="sf-crumbs">
            <li>列表技巧</li>
            <li>列表排序</li>
        </ul>
        <div class="sf-panel sf-pb15 sf-pt5 sf-mv10 sf-mh15">
            <table class="sf-table">
                <tr>
                    <th>商户编号 <i class="fa fa-sort sf-color-primary sf-ph5" sf-pointer @click="sort('guid')"></i></th>
                    <th>注册号 <i class="fa fa-sort sf-color-primary sf-ph5" sf-pointer @click="sort('date')"></i></th>
                    <th>商户名称 <i class="fa fa-sort sf-color-primary sf-ph5" sf-pointer @click="sort('name')"></i></th>
                </tr>
                <tr v-for="(item, index) in listdata">
                    <td v-text="item.guid"></td>
                    <td v-text="item.date"></td>
                    <td v-text="item.name"></td>
                </tr>
            </table>
            <sf-pagination :total="total" :page-size="size" class="sf-mt10" v-model="page" @input="updateList"></sf-pagination>
        </div>
    </div>
</template>

<script>
import axios from '../utils/axios'
export default {
    data() {
        return {
            listdata: [],
            selected: [],
            size: 10,
            total: 0,
            page: 1,
        }
    },
    async created() {
        await this.updateList();
    },
    methods: {
        async updateList() {
            this.loading = true;
            var res = (await axios.get(
                'http://10.100.158.183:1234/_list/guid-name-date',
                Object.assign({}, this.filter, {
                    page: this.page
                })
            )).data
            this.total = res.total;
            this.listdata = res.data;
            // 清空被选中的项目
            this.selected = []
        },
        sort(key) {
            if(this.listdata.length < 3) return ;
            if(this.listdata[0][key] > this.listdata[1][key]) {
                this.listdata = this.listdata.sort((x,y)=>x[key]>y[key]);
            }else {
                this.listdata = this.listdata.sort((x,y)=>x[key]<y[key]);
            }
            
        }
    }
}
</script>

<style lang="css">
</style>



