<template lang="html">
    <i class="fa fa-gears sf-color-primary sf-theme-setter" title="点击切换皮肤" @click="handler"></i>
</template>
<script>
export default {
    name: 'theme-setter',
    props: {
        themes: {
            default() {
                return [];
            }
        }
    },
    data() {
        return {
            index: 0
        }
    },
    computed: {

    },
    methods: {
        setTheme(name) {
            this.themes.forEach(item=>{
                document.body.classList.remove('sf-theme-'+item);
            });
            document.body.classList.add('sf-theme-'+name);
        },
        handler() {
            if(this.themes.length === 0) return;
            this.index = (this.index+1)%this.themes.length;
            this.setTheme(this.themes[this.index]);
        }
    }
}
</script>



