<template lang="html">
    <ul :class="{
        'sf-menuv': true,
        'sf-menuv--collapse': isCollapse
    }">
        <slot></slot>
    </ul>
</template>

<script>
// FIXME: collapsed 时候 ，子菜单超出视图范围的处理
import isArray from 'lodash/isArray'
export default {
    name: 'menuv',
    props: {
        // 可选 normal, collapse
        mode: {
            type: String,
            default: 'normal'
        }
    },
    data() {
        return {
            level: 0,
        }
    },
    computed: {
        isNormal() {
            return this.mode === 'normal';
        },
        isCollapse() {
            return this.mode === 'collapse';
        }
    },
    mounted() {
        this.addLevel();
        // 将 mode 传递给item
        this.$children.forEach(item=>{
            if(item.$options.name === 'menuv-item') {
                item.mode = this.mode;
            }
        })
    },
    watch: {
        mode() {
            // 将 mode 传递给item
            this.$children.forEach(item=>{
                if(item.$options.name === 'menuv-item') {
                    item.mode = this.mode;
                }
            })
        }
    },
    methods: {
        addLevel() {
            this.level++;
            if(isArray(this.$slots.default)) {
                this.$slots.default.forEach(item=>{
                    if(!item.child) return;
                    item.child.addLevel();
                })
            }
        }
    }
}
</script>



