<template lang="html">
  <div>
        <ul class="sf-crumbs">
            <li><a href="#/list">商户列表</a></li>
            <li>商户新增</li>
        </ul>
        <div class="sf-panel sf-pb20 sf-mv10 sf-mh15">
            <div class="sf-panel-header" >
                商户基本信息
            </div>
            <sf-validator ref="validator">
                <form>
                    <form-group>
                        <label>商户名称</label>
                        <sf-validator-message
                                :value="form.name"
                                :rules="{required: '商户名称必须填写'}"
                            >
                            <input type="text" class="sf-input" v-model="form.name">
                        </sf-validator-message>
                    </form-group>
                    <form-group>
                        <label>商户类型</label>
                        <sf-select v-model="form.type">
                            <sf-option v-for="type in mTypes" :value="type" :key="type"></sf-option>
                        </sf-select>
                    </form-group>
                    <form-group>
                        <label>注册号</label>
                        <input type="text" class="sf-input sf-w300" v-model="form.number">
                    </form-group>
                </form>
            </sf-validator>

            <p class="sf-tc sf-mt40">
                <button type="button" class="sf-button" @click="submit">提交</button>
            </p>
        </div>
    </div>
</template>

<script>
import store from '../store'
import axios from '../utils/axios'
export default {
    name: 'Form',
    data() {
        return {
            form: {
                number: '',
                type: '无',
                name: '',
                id: Date.now()
            }
        }
    },
    async created() {
        // 有 id 则为编辑
        if(this.id) {
            let res = await axios('/customers/'+this.id);
            this.form.number = res.data.id;
            this.form.type = res.data.type;
            this.form.name = res.data.name;
        }
    },
    computed: {
        mTypes() {
            return store.mTypes;
        },
        id() {
            return this.$route.query.id;
        }
    },
    methods: {
        async submit() {
            // 强制对表单进行校验，返回校验的结果
            var r = await this.$refs.validator.forceValid();
            if(!r) return ;
            try {
                if(this.id) {
                    await axios.put(`/customers/${this.id}`, this.form)
                    this.$toast('编辑完成');
                }else {
                    await axios.post('/customers', this.form)
                    this.$toast('添加成功');
                }
                this.$router.push('/list');
            }catch(e) {
                this.$toast('添加失败', 'danger');
            }
        }
    }
}
</script>

<style lang="css">
</style>



