<template lang="html">
    <div class="sf-input-box" :class="{'sf-input-box--focus': isFocus}" @click="focus()">
        <slot></slot>
        <input
            :style="{width: inputWidth+'em'}"
            class="sf-input-box__input"
            :class="{'sf-input-box__input--hidden': !editable}"
            :readonly="!editable"
            :value="value"
            @input="$emit('input', $event.target.value), inputHandler($event)"
            @focus="beFocus(),$emit('focus'), selectText()"
            @blur="beBlur(),$emit('blur')"
            @keydown.enter.prevent="addItem(),$emit('enter')"
            @keydown.delete="removeItem()"
            @keydown.down.prevent="$emit('down')"
            @keydown.up.prevent="$emit('up')"
            @keydown.esc.prevent="$emit('esc')"
            @keydown.tab="$emit('tab')"
            ref="inputer"
        >
    </div>
</template>

<script>
export default {
    name: 'input-box',
    props: {
        // 输入的值
        value: {
            type: String,
            default: ''
        },
        // 是否可以编辑
        editable: {
            type: Boolean,
            default: true
        },
        // 指示箭头,  '' 'up' 'down'
        arrow: {
            type: String,
            default: ''
        },
        // 是否有清除按钮
        clearable: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            isFocus: false,
            notSelect: false,
            textLength: 0
        }
    },
    computed: {
        inputWidth() {
            return this.value.length + 1;
        }
    },
    methods: {
        inputHandler(event) {
            this.textLength = event.target.value.length;
        },
        beFocus() {
            this.isFocus = true;
        },
        beBlur() {
            this.isFocus = false;
        },
        // 选中输入框中的文字
        selectText() {
            if(this.notSelect) {
                this.notSelect = false;
                return;
            }
            this.$refs.inputer.select();
        },
        // 清除输入的内容
        clearText() {
            this.$emit('input', '');
        },
        // 添加项目
        addItem() {
            if(this.value === '') return;
            this.$emit('add');
        },
        // 删除项目
        removeItem() {
            if(this.value === '') this.$emit('remove');
        },
        focus(notSelect = false) {
            this.notSelect = notSelect;
            this.$refs.inputer.focus();
        },
        blur(notSelect = false) {
            this.$refs.inputer.blur();
        }
    }
}
</script>



