/**
 * 应用的路由管理
 */

import VueRouter from 'vue-router'
import Vue from 'vue'

Vue.use(VueRouter);

export default new VueRouter({
    routes: [{
        path: '/',
        component: require('../pages/home/Home.vue')
    }, {
        path: '/home',
        component: require('../pages/home/Home.vue')
    }, {
        path: '/list',
        component: require('../pages/List.vue'),
        beforeEnter(to, from, next) {
            // 对用户权限进行校验
            if(true) {
                next();
            }else {
                window.location.hash = '#/error/403'
            }
        }
    }, {
        path: '/form',
        component: require('../pages/Form.vue')
    }, {
        path: '/lazy',
        // 对页面进行按需加载配置
        component: resolve => require(['../pages/Lazy.vue'], resolve)
    }, {
        path: '/nest',
        component: require('../pages/Nest.vue'),
        children: [{
            path: 'child1',
            component: require('../pages/NestChild1.vue')
        },{
            path: 'child2',
            component: require('../pages/NestChild2.vue')
        }]
    },{
        path: '/error/403',
        component: require('../pages/Error403.vue')
    }, {
        name: 'reload',
        path: '/reload',
        component: {
            name: 'reload',
            template: '<div></div>',
            mounted() { this.$router.back(); }
        }
    }, {
        path: '/list-layout',
        component: require('../pages/ListLayout.vue')
    }, {
        path: '/list-sort',
        component: require('../pages/ListSort.vue')
    }, {
        path: '/tp-wangeditor',
        component: require('../pages/TpWangEditor.vue')
    }, {
        path: '/news',
        component: require('../pages/news/News')
    }, {
        path: '/my-milestone',
        component: require('../pages/milestone/MyMilestone')
    }, {
        path: '/dept-milestone',
        component: require('../pages/milestone/DeptMilestone')
    }, {
        path: '/schedule',
        component: require('../pages/schedule/Schedule')
    }, {
        path: '/plan',
        component: require('../pages/plan/Plan')
    },  {
        path: '/my-daily',
        component: require('../pages/daily/MyDaily')
    }, {
        path: '/daily-statis',
        component: require('../pages/daily/DailyStatis')
    }, {
        path: '/daily-statis/add',
        component: require('../pages/daily/Add')
    }, {
        path: '/daily-statis/detail',
        component: require('../pages/daily/Detail')
    }, {
        path: '/daily-statis/edit',
        component: require('../pages/daily/Edit')
    }]
});



