<template lang="html">
    <div
        class="sf-scale-area"
        @mousewheel.prevent="mousewheelHandler"
        @dragover.prevent
        @dragend.prevent
    >
        <div
            class="sf-scale-area__content"
            :style="{'transform': transform}"
            @dragstart="dragstartHandler"
            @drag.prevent="dragHandler"
        >
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
    name: 'scale-area',
    data() {
        return {
            // 水平位置
            x: 0,
            // 垂直位置
            y: 0,
            // 缩放比例
            scale: 1,
            // 旋转角度
            rotate: 0,
            // 鼠标位置
            mousePostion: {
                x: 0,
                y: 0
            }
        }
    },
    computed: {
        transform() {
            return `
              translateX(${this.x}px)
              translateY(${this.y}px)
              scale(${this.scale})
              rotate(${this.rotate}deg)`;
        }
    },
    methods: {
        // 拖拽开始
        dragstartHandler(event) {
            this.mousePostion = {
                x: event.clientX,
                y: event.clientY
            };
        },
        // 拖拽中
        dragHandler(event) {
            if (event.clientX === 0 && event.clientY === 0) return;

            this.move(event.clientX - this.mousePostion.x, event.clientY - this.mousePostion.y);

            this.mousePostion = {
                x: event.clientX,
                y: event.clientY
            };
        },
        mousewheelHandler(event) {
            this.scaleIt(event.deltaY / 1000);
        },
        // 移动内容
        move(offsetX, offsetY) {
            this.x += offsetX;
            this.y += offsetY;
        },
        // 缩放内容
        scaleIt(factor) {
            this.scale += factor;
            if (this.scale < 0) this.scale = 0;
        },
        // 重置位置
        reset() {
            // 水平位置
            this.x = 0;
            // 垂直位置
            this.y = 0;
            // 缩放比例
            this.scale = 1;
            // 旋转角度
            this.rotate = 0;
            // 鼠标位置
            this.mousePostion = {
                x: 0,
                y: 0
            }
        },
        // 选装
        roateIt(offsetDeg) {
            this.rotate+=offsetDeg;
        }
    }
}
</script>



