<template lang="html">
    <div class="sf-validator">
        <slot></slot>
    </div>
</template>

<script>

import isArray from 'lodash/isArray'

export default {
    name: 'validator',
    props: {

    },
    mounted() {
    },
    methods: {
        // 获取所有 validator-message
        _getValidatorMessages() {
            var validatorMessages = [];
            (function visit(children) {
                if(isArray(children)) {
                    children.forEach(item=>{
                        if(item.$options.name==='validator-message') {
                            validatorMessages.push(item);
                        }
                        visit(item.$children);
                    })
                }
            })(this.$children);
            return validatorMessages;
        },
        // 重置校验
        reset() {
            var validatorMessages = this._getValidatorMessages();
            this.$nextTick(()=>{
                validatorMessages.forEach(item=>{
                    item.reset();
                })
            })

        },
        // 校验
        async forceValid() {
            var validatorMessages = this._getValidatorMessages();
            // validatorMessages[0].valid();
            var rs = await Promise.all(validatorMessages.map(item=>item.valid()));
            // TODO: 聚焦到第一个校验没有通过的输入框 等待表单组件的 focus 状态完善
            return rs.reduce((a,item)=>a&&item);
        }
    }
}
</script>



