<template lang="html">
    <div :style="{
            'flex': flex,
            'overflow': overflow,
            'display': display,
            'justify-content': justifyContent
        }"
        class="sf-container"
    >
        <slot></slot>
    </div>
</template>

<script>
import toLength from '../../utils/toLength'
import isNumber from 'lodash/isNumber'
import isString from 'lodash/isString'
var valign2JustifyContent= {
    top: 'flex-start',
    bottom: 'flex-end',
    center: 'center',
    justify: 'space-between'
}
export default {
    name: 'container',
    props: {
        // span 如果不写则默认是内容撑开的，如果为 stretch 则为撑开，或者设置px的值
        span: {
            type: [String, Number],
            default: 'initial'
        },
        isScroll: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        height_: function() {
            var x = toLength(this.span);
            return toLength(this.span);
        },
        flex: function() {
            if(isNumber(this.span) && this.span >= 1) {
                return this.span.toString();
            }
            if(isString(this.span) && this.span === 'stretch') {
                return '1';
            }
            return 'none';
        },
        overflow: function() {
            if(this.isScroll) {
                return 'auto';
            }else {
                return 'visible';
            }
        },
        justifyContent() {
            return valign2JustifyContent[this.valign];
        }
    },
    data() {
        return {
            isContainer: true,
            display: 'block'
        }
    },
    mounted() {
        // 如果子元素中含有容器，则容器的display=flex
        if(this.$children.some(item=>item.isContainer)) {
            this.display = 'flex'
        }
    },
    updated() {
        if(this.$children.some(item=>item.isContainer)) {
            this.display = 'flex'
        }
    }
}
</script>




