<template lang="html">
    <div>
        <ul class="sf-crumbs">
            <li>日报</li>
            <li>日报统计</li>
        </ul>
        <div class="sf-panel sf-mv10 sf-mh15 sf-pv5">
            <sf-condition-box :height="39" foldable>
                <form fluid>
                    <form-group>
                        <label>姓名</label>
                        <input type="text" class="sf-input" v-model="paging.condition.userName" @keydown.enter="searchInfoByPage">
                    </form-group>
                    <form-group>
                        <label>性别</label>
                        <sf-select v-model="paging.condition.sex" @input="searchInfoByPage">
                            <sf-option :value="''" title="无"></sf-option>
                            <!--<sf-option v-for="type in mTypes" :value="type" :key="type"></sf-option>-->
                            <sf-option value="female" key="female"></sf-option>
                            <sf-option value="male" key="male"></sf-option>
                        </sf-select>
                    </form-group>
                    <form-group>
                        <label>地址</label>
                        <input type="text" class="sf-input" v-model="paging.condition.address" @keydown.enter="searchInfoByPage">
                    </form-group>
                    <form-group>
                        <label>创建时间</label>
                        <sf-datepicker v-model="paging.condition.startTime" :range-to="paging.condition.endTime"></sf-datepicker>
                        &nbsp;_&nbsp;
                        <sf-datepicker v-model="paging.condition.endTime" :range-from="paging.condition.startTime"></sf-datepicker>
                    </form-group>
                </form>
                <button slot="corner" type="button" class="sf-button sf-sm" @click="searchInfoByPage"><i class="fa fa-search"></i> 查询</button>
            </sf-condition-box>
        </div>
        <div class="sf-panel sf-mv10 sf-mh15 sf-pv10">
            <ul class="sf-toobar">
                <li @click="$router.push('/daily-statis/add')"><i class="fa fa-plus sf-color-primary"></i> 新增</li>
            </ul>
        </div>
        <div class="sf-panel sf-pb15 sf-pt5 sf-mv10 sf-mh15" v-show="state === 'loaded'">
            <table class="sf-table">
                <tr>
                    <th class="sf-tc">序号</th>
                    <th>用户id</th>
                    <th>姓名</th>
                    <th>性别</th>
                    <th>地址</th>
                    <th>创建时间</th>
                    <th>操作</th>
                </tr>
                <tr v-for="(item, index) in userInfo">
                    <td class="sf-tc sf-w40" v-text="index+(paging.pageIndex-1)*paging.pageSize+1"></td>
                    <td v-text="item.userId"></td>
                    <td v-text="item.userName"></td>
                    <td v-text="item.sex"></td>
                    <td v-text="item.address"></td>
                    <td>{{item.createTime | moment}}</td>
                    <td>
                        <button class="sf-button sf-primary-o sf-sm" @click="$router.push({path:'/daily-statis/detail', query: {id:item.recId}})">详情</button>
                        <button class="sf-button sf-primary-o sf-sm" @click="$router.push({path:'/daily-statis/edit', query: {id:item.recId}})">编辑</button>
                        <button class="sf-button sf-danger-o sf-sm" @click="remove(item.recId)">删除</button>
                    </td>
                </tr>
            </table>
            <sf-pagination :total="paging.totalCount" :page-size="paging.pageSize" class="sf-mt10" v-model="paging.pageIndex" @input="getInfoByPage"></sf-pagination>
        </div>
    </div>
</template>

<script>
    import moment from 'moment'
    import store from '../../store'
    import axios from '../../utils/axios'
    export default {
        name: 'DailyStatis',
        data() {
            return {
                userInfo: [],
                paging: {
                    pageIndex: 1, // 分页页码
                    totalCount: 0, // 总条数
                    pageSize: 2, // 每页的数据量
                    condition: {
                        userName: '',
                        sex: '',
                        address: '',
                        startTime: '',
                        endTime: ''
                    }
                },
                // init loaded loading
                state: 'init',
            }
        },
        computed: {
            mTypes() {
                return store.mTypes;
            }
        },
        created() {
            $.ajax({
                url: 'http://localhost:8011/milestone/admin/loadList',
                type: 'post',
                contentType: 'application/json',
                data: JSON.stringify(this.paging)
            }).then(function(res){
                this.userInfo = res.content;
                this.paging.totalCount = res.totalCount;
                this.state = 'loaded';
            }.bind(this));
        },
        methods: {
            searchInfoByPage() {
                this.paging.pageIndex = 1;
                $.ajax({
                    url: 'http://localhost:8011/milestone/admin/loadList',
                    type: 'post',
                    contentType: 'application/json',
                    data: JSON.stringify(this.paging)
                }).then(function(res){
                    this.userInfo = res.content;
                    this.paging.totalCount = res.totalCount;
                    this.state = 'loaded';
                }.bind(this));
            },
            getInfoByPage() {
                $.ajax({
                    url: 'http://localhost:8011/milestone/admin/list',
                    type: 'post',
                    contentType: 'application/json',
                    data: JSON.stringify(this.paging)
                }).then(function(res){
                    this.userInfo = res;
                }.bind(this));
            },
            remove(recId) {
                this.$confirm('确认要删除吗？').then((r) => {
                    if (r) {
                        $.ajax({
                            url: 'http://localhost:8011/milestone/admin/delUser',
                            type: 'post',
                            data: {recId: recId}
                        }).then(function(res){
                            if (res.code == 0) {
                                this.$toast('删除成功');
                                this.paging.pageIndex = 1;
                                this.searchInfoByPage();
                            } else {
                                this.$toast('删除失败', 'danger');
                            }
                        }.bind(this));
                    }
                });

            },

        },
    }
</script>

<style lang="css">

</style>


