<template lang="html">
    <transition name="fadescale">
        <div
            class="sf-modal modal"
            :class="{'state-inner-scroll':innerScroll, 'sf-modal--inner-scroll':innerScroll}"
            v-if="isOpen"
            @click.self="easyClose?_close(false):''"
        >
            <div class="sf-modal__content modal-content" :style="{'width': width+'px'}">
                <div class="sf-modal__header modal-header" v-show="hasHeader">
                    <div class="sf-modal__title modal-title">
                        <slot name="header">{{title}}</slot>
                    </div>
                </div>
                <div class="sf-modal__body modal-body">
                    <slot></slot>
                </div>
                <div class="sf-modal__footer modal-footer" v-show="hasFooter">
                    <slot name="footer"></slot>
                </div>
                <div class="sf-modal__close modal-close" @click="_close(false)">
                    <i class="fa fa-close"></i>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    import isUndefined from 'lodash/isUndefined'
    var modalQueue = [];

    export default {
        name: 'modal',
        data: function () {
            return {
                isOpen:false
            }
        },
        props: {
            // 接受一个回调函数，返回 promise 对象， 如果该对象返回true 则关闭，否则不关闭
            confirm: {
                type: Function,
                default: function() {
                    return Promise.resolve(true)
                }
            },
            // 是否内部滚动条
            innerScroll: {
                type: Boolean,
                default: false
            },
            // 模态窗的宽度
            width: {
                type: Number,
                default: 600
            },
            // 模态窗的标题
            title: {
                type: String,
                default: ''
            },
            // 点击关闭弹窗
            easyClose: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            hasFooter() {
                return !!this.$slots.footer;
            },
            hasHeader() {
                return !!this.$slots.header||this.title;
            }
        },
        methods: {
            open(){
                if(this.isOpen === true) return;

                // 处理窗口队列
                if(!isUndefined(modalQueue[0])){
                    modalQueue[0].isOpen = false;
                    var durationg = 300;
                }else {
                    var durationg = 0;
                }
                setTimeout(()=>{
                    // 打开窗口
                    this.isOpen = true;
                    // 触发事件
                    this.$emit('open');
                    // 禁用 body 滚动
                    document.body.classList.add('sf-notscroll');
                },durationg);
                modalQueue.unshift(this);
            },
            _close: async function(force){
                if(this.isOpen === false) return;

                // 进行确认
                if(!force) {
                    if(! await this.confirm()) {
                        return;
                    }
                }

                // 关闭窗口
                this.isOpen = false;
                // 触发事件
                this.$emit('close');
                // 启用 body 滚动
                document.body.classList.remove('sf-notscroll');
                // 处理窗口队列
                modalQueue.shift();
                setTimeout(()=>{
                    if(modalQueue.length === 0) return;
                    modalQueue[0].isOpen = true;
                },300)
            },
            close() {
                this._close(false);
            },
            // 强制关闭，越过关闭提示
            closeForce() {
                this._close(true);
            }
        }
    }
</script>

<style lang="less">
    .sf-modal {
        z-index: 20;
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, .5);
        opacity: 1;
        transform: scale(1);
        overflow-y: auto;
        padding: 60px 0;
        box-sizing: border-box;
        // 消除滚动条跳跃
        padding-left: calc(100vw - 100%);
        &::-webkit-scrollbar {
            display: none;
        }
    }

    .sf-modal__content {
        overflow: hidden;
        box-sizing: border-box;
        width: 600px;
        margin:0 auto;
        vertical-align: middle;
        background-color: white;
        box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
        position: relative;
    }

    .sf-modal__header {
        box-sizing: border-box;
        background-color: #dedede;
        line-height: 2em;
        padding: 0.5em 1.25em;
        position: relative;
    }

    .sf-modal__title {
        font-weight: bold;
        margin: 0;
        padding: 0;
        line-height: 1em;
        box-sizing: border-box;
    }

    .sf-modal__body {
        padding: 0.75em 1.25em;
        box-sizing: border-box;
    }
    .sf-modal__footer {
        padding: 0.75em 1.5em;
        border-top: 1px solid #ccc;
        text-align: right;
        box-sizing: border-box;
    }
    .sf-modal__close {
        position: absolute;
        width: 3em;
        line-height: 2em;
        text-align: center;
        top: 0;
        right: 0;
        font-size: 14px;
        cursor: pointer;
        box-sizing: border-box;
        &:hover {
            color: #999;
        }
        i {
            line-height: 2em;
        }
    }

    // 内部滚动状态
    .sf-modal--inner-scroll {
        .sf-modal__content {
            max-height: 100%;
            overflow: hidden;
            display: flex;
            flex-direction: column;
        }
        .sf-modal__body {
            flex: 1;
            overflow-y: auto;
        }
        .sf-modal__footer {
            box-sizing: content-box;
        }
    }

    // 过渡效果
    .fadescale-enter-active, .fadescale-leave-active {
        transition: all .3s;
    }
    .fadescale-enter, .fadescale-leave-to {
        opacity: 0;
        transform: scale(1.05);
    }
    .sf-notscroll {
        overflow: hidden!important;
    }
</style>



