<template lang="html">
	<span class="sf-upload">
		<span @click="uploadClick">
			<slot></slot>
			<input type="file" class="sf-upload__input" :multiple="!single" :accept="accept" @change="changeHandler">
		</span>

		<!-- 单行文本模式 -->
		<ul class="sf-upload__list" v-if="listType === 'text'">
			<li class="sf-upload__item" v-for="file in filesQueue">
				<div class="sf-upload__info" >
					<a :href="file.response||'javascript:;'" target="_blank" class="sf-upload__link">
						<i class="fa fa-paperclip sf-upload__icon"></i>
						<span class="sf-upload__title" :title="file.name">{{file.name}}</span>
					</a>
					<i class="fa fa-times sf-upload__close" @click="removeHandler(file, filesQueue)"></i>
				</div>
				<div class="sf-upload__item-bottom" v-if="file.showProgress">
					<div class="sf-upload__progress">
						<div class="sf-upload__progress-bar" :style="{width: file.percentage+'%'}"></div>
					</div>
				</div>
			</li>
		</ul>
		<!-- 图片预览模式 -->
		<ul class="sf-upload__list" v-if="listType === 'picture'">
			<li class="sf-upload__box" v-for="file in filesQueue">
				<a class="sf-upload__thumbnail" v-if="file.isImage && file.url" :href="file.response||'javascript:;'"  target="_blank">
					<img :src="file.url">
				</a>
				<i class="fa fa-picture-o sf-upload__thumbnail" v-else></i>
				<div class="sf-upload__box-right">
					<span class="sf-upload__title" :title="file.name">{{file.name}}</span>
					<div class="sf-upload__progress" v-if="file.showProgress">
						<div class="sf-upload__progress-bar" :style="{width: file.percentage+'%'}"></div>
					</div>
				</div>
				<i class="fa fa-times sf-upload__close" @click="removeHandler(file, filesQueue)"></i>
		    </li>
		</ul>
	</span>
</template>

<script>

// TODO: 限制文件上传数量
// TODO: 限制文件文件大小
// TODO: 编辑功能 （待设计）
// TODO: 拖拽功能
// TODO: remove 事件在取消过程中也触发(待议)
// FIXME: 如果上传同一个文件，删除一个文件，所有文件被删除 ✅

import empty from 'ramda/src/empty'
import slice from 'ramda/src/slice'
import append from 'ramda/src/append'
import without from 'ramda/src/without'
function noop() {}
export default {
    name: 'upload',
	data () {
		return {
			// 文件队列
			filesQueue: [],
			tmpIndex: 1
		}
	},
    computed: {
        model: {
            set(val) {
                this.$emit('input', val);
            },
            get() {
                return this.value;
            }
        }
    },
	props: {
        value: {
			type: [Array, Object],
            default() {
				return []
			}
        },
		// 发到后台的文件参数名，默认是'file'
		paramName: {
			type: String,
			default: 'file'
		},
		// 上传图片的url
		action: {
			type: String,
			default: '',
			required: true
		},
		// 是否为单文件上传
		single: {
			type: Boolean,
			default: false
		},
		//TODO: 上传的类型，支持'select'和'drag'，默认为'select'
		type: {
			type: String,
			default: 'select'
		},
		// 接受上传的文件类型
		accept: {
			type: String,
			default: ''
		},
		// 上传列表的内建样式，支持'text'和'picture'，默认为'text' 'none'
		listType: {
			type: String,
			default: 'text'
		},
		// 对响应的内容进行处理
		pipe: {
			type: Function,
			default: function(value) {
				return value;
			}
		},
		// // 默认显示的文件内容
		// // [{name:'', url: ''}]
		// fileList: {
		// 	type: Array,
		// 	default() {
		// 		return [];
		// 	}
		// },
		// // 默认显示的文件内容
		// // {name:'', url: ''}
		// file: {
		// 	type: Object,
		// 	default() {
		// 		return null;
		// 	}
		// }
	},
	created() {
		if(this.single) {
			this.filesQueue.push({
				response: this.value.url,
				url: this.value.url,
				name: this.value.name,
				showProgress: false,
				isImage: true
			})
		}else {
			this.filesQueue = this.value.map(item=>{
				return {
					response: item.url,
					url: item.url,
					name: item.name,
					showProgress: false,
					isImage: true
				}
			})
		}
	},
	methods: {
		open() {
			this.uploadClick();
		},
		uploadClick() {
            this.$el.querySelector('.sf-upload__input').click();

		},
		// 改变文件选择时，触发文件上传
		changeHandler(event) {
			var fileList = event.target.files
			for (let file of fileList) {
				this.upload(file)
			}
            // 清空 input value, 否则在上传同一个文件的时候，此事件无法触发
            this.$el.querySelector('.sf-upload__input').value = '';

		},
		upload(file) {
			var xhr = new XMLHttpRequest(), formData, self = this, fileReader
			var _file = {
				// status用于标识文件的状态，有'uploading', 'abort', 'done', 'error'
				status: 'uploading',
				name: file.name,
				type: file.type,
				isImage: /^image/.test(file.type),
				size: file.size,
				percentage: 0,
				uid: Date.now() + this.tmpIndex++,
				showProgress: true,
				url: '',
				xhr: xhr
			}
			if (window.FormData) {
				formData = new FormData()
				formData.append(self.paramName, file)
				if(this.single) {
					this.filesQueue = []
				}
				this.filesQueue.push(_file)
			}
			if (window.FileReader) {
				fileReader = new FileReader()
				fileReader.onload = function(event) {
					_file.url = event.target.result
				}
				fileReader.readAsDataURL(file)
			}
			xhr.upload.onprogress = function(event) {
				var perc = (event.loaded / event.total) * 100
				// self.updateProperty(_file.uid, 'percentage', perc)
				_file.percentage = perc
			}
			// 传送成功时的回调
			xhr.onload = (event) => {
				if (xhr.status === 200) {
					_file.status = 'done'
					_file.showProgress = false
					_file.response = this.pipe(event.target.response)
					if(!this.single) {
						this.model = append({
							url:_file.response,
							name: _file.name
						}, this.model)
					}else {
						this.model = {
							url:_file.response,
							name: _file.name
						}
					}
                    this.$emit('success', _file)
				} else {
					_file.status = 'error'
                    this.$emit('error', _file)
				}
			}
			// 上传失败时的回调
			xhr.onerror = (event) => {
				_file.status = 'error'
                // 上传失败也应该显示，提示上传失败项目
				this.filesQueue.splice(this.filesQueue.indexOf(_file), 1)
                this.$emit('error', _file)
			}
			xhr.onabort = (event) => {
                this.$emit('abort', _file)
			}
			xhr.open('POST', self.action)
			xhr.send(formData)
		},
		removeHandler(file, filesQueue) {
			file.xhr&&file.xhr.abort()
            var dropIndex = filesQueue.indexOf(file);
			filesQueue.splice(dropIndex, 1)
			if(this.single) {
				this.model = null
			}else {
				this.model = without(slice(dropIndex, dropIndex+1, this.model),this.model)
			}
            this.$emit('remove', file)
		},
        clear() {
			if(this.single) {
				this.model = null
			}else {
				this.model = []
			}

			this.filesQueue = []
        }
	}
}
</script>


