<template lang="html">

</template>

<script>

import isFunction from 'lodash/isFunction'

export default {
    name: 'datatable-detail',
    props: {
        // 列的宽度
        width: {
            type: [Number, String],
            default: ''
        }
    },
    data() {
        return {
            // 单元格的类型，可选 index, checkbox, radio, normal
            type: 'detail',
            detailRender: function(props) {
                return '详情信息...';
            },
            // 已经被展开的条目
            expandItems: []
        }
    },
    mounted() {
        // 判断是否需要获取单元格模板
        if(isFunction(this.$scopedSlots.default)) {
            this.detailRender = this.$scopedSlots.default;
        }
    }
}
</script>



