export  default {
    name: 'step',
    props: {
        title: {
            type: String,
            default: ''
        },
        index: {
            type: Number, 
            default: 1
        }
    },
    data() {
        return {
            // 状态, none ready actived
            status: 'none'
        }
    },
    mounted() {
        this.updateStatus(this.$parent.value);
        this.$parent.$watch('value',index=>{
            this.updateStatus(index);
        })
    },
    methods: {
        active() {
            if(this.status === 'passed') {
                this.$parent.$emit('input', this.index);
            }
        },
        updateStatus(index) {
            if(index === this.index) {
                this.status = 'actived'
            }else if(this.index < index) {
                this.status = 'passed'
            }else {
                this.status = 'none'
            }
            
        }
    },
    render(h) {
        return <li class="sf-step" x-status={ this.status } onClick={this.active}>
                <span class="sf-step__index">{ this.status==='passed'?<i class="fa fa-check"></i>:this.index }</span> <span class="sf-step__title">{ this.title }</span>
            </li>
    }
}


