<script>

// TODO: 支持自定义 title
// TODO: 展开功能
// TODO: 单选
// TODO: 多选
import isArray from 'isArray'
import node from './node.vue'
export default {
    name: 'tree',
    props: {
        data: {
            type: Array,
            default() {
                return null
            }
        },
        pipe: {
            type: Function,
            default: function(value) {
                return value;
            }
        }
    },
    components: { node },
    methods: {
        expandAll() {
            this.$children.forEach(node=>{
                node.expand(true);
            })
        },
        shrinkAll() {
            tthis.$children.forEach(node=>{
                node.shrink();
            })
        },
        renderNode(h, nodeData) {
            var rawData = nodeData;
            nodeData = this.pipe(nodeData)
            var children = nodeData.children;
            return h('node', {
                props: {
                    title: nodeData.title
                },
                on: {
                    click:()=>{
                        this.$emit('click-node', rawData)
                    }
                }
            }, isArray(children)?children.map(node=>this.renderNode(h, node)):null)
        }
    },
    render(h) {
        if(isArray(this.data)) {
            return h('tree-root', this.data.map(node=>this.renderNode(h, node)))
        }else {
            return h('tree-root', this.$slots.default)
        }
    }
}
</script>





