<template lang="html">
    <div class="sf-toast">
        <transition name='fade'>
            <div
                :class="['sf-toast__item', 'sf-toast__item--'+message.scene]"
                v-for="message in showQueue"
            >
                <span class="sf-toast__header">
                    <i class="fa" :class="[{
                        'fa-info': message.scene === 'default',
                        'fa-check': message.scene === 'success',
                        'fa-exclamation': message.scene === 'warn',
                        'fa-times': message.scene === 'danger'
                    }]"></i>
                </span>
                <div class="sf-toast__content">
                    {{message.message}}
                </div>
                <div
                    class="sf-toast__close"
                    @click="close(message)"
                    v-if="hasCloseBtn"
                >
                    <i class="fa fa-close"></i>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name:'toast',
        props:{
            // 是否有关闭按钮
            hasCloseBtn: {
                type: Boolean,
                default: false,
                coerce: toBoolean
            },
            // 一次最多显示的吐司框数量
            showNumber: {
                type:Number,
                default: 3
            },
            // 每个消息显示的时限, 单位毫秒
            showDuration: {
                type:Number,
                default: 2000
            }
        },
        data: function () {
            return {
                // 消息队列
                messageQueue: [],
                // 当前显示的消息
                showQueue: []
            }
        },
        created() {
            this.ask();
        },
        methods: {
            // 关闭当前吐司框
            close(message) {
                // 删除显示队列中的信息
                this.showQueue.splice(this.showQueue.indexOf(message),1);
                // 进行一次询问
                this.ask();

            },
            // 将一条信息推入队列
            push(message) {
                // 推入消息队列
                this.messageQueue.push(message);
                // 进行一次询问
                this.ask();
            },
            // 询问现在是否有可以显示的内容，并且有空余的位子来显示，有则进行显示
            ask() {
                if(this.messageQueue.length > 0 && this.showQueue.length < this.showNumber) {
                    var loop = Math.min(this.showNumber - this.showQueue.length, this.messageQueue.length);
                    while(loop--){
                        var newMessage = this.messageQueue.shift();
                        this.showQueue.push(newMessage);
                        // 设置显示的消息的时限，时限到了则进行删除
                        ((newMessage)=>{
                            setTimeout(()=>{
                                this.close(newMessage);
                            }, this.showDuration)
                        })(newMessage)
                    }
                }
            }
        }
    }

    function toBoolean(value){
        if(value === "false"){
            return false;
        }

        if(value === "true"){
            return true;
        }

        return value;
    }
</script>



