<template lang="html">
    <div class="sf-expand" @click="$emit('click')">
        <slot></slot>
        <transition name="fade">
            <box :max-height="maxHeight" :width="width" v-show="show" ref="box">
                <slot name="content"></slot>
            </box>
        </transition>
    </div>
</template>
<script>
    import Popper from 'popper.js'
    import box from './box.vue'
    export default {
        name: 'expand',
        props:{
            // 宽度 默认0 如果为0则宽度和slot宽度相等
            width: {
                type: Number,
                default: 0
            },
            // 最大高度，默认最大高度为撑满滚动条的容器
            maxHeight: {
                type: Number,
                default: 350
            },
            show: {
                type: Boolean,
                default: false
            },
            // 可选 bottom-start bottom-end
            placement: {
                type: String, 
                default: 'bottom-start'
            }
        },
        data() {
            return {
                popper: null
            }
        },
        watch: {
            width() {
                this.$nextTick(()=>{
                    this.popper&&this.popper.update();
                })
            },
            maxHeight() {
                this.$nextTick(()=>{
                    this.popper&&this.popper.update();
                })
            },
            show() {
                if(this.width === 0) {
                    this.$refs.box.$el.style.width = this.$bind.offsetWidth + 'px';
                }else{
                    this.$refs.box.$el.style.width = this.width + 'px';
                }
                this.$nextTick(()=>{
                    this.popper&&this.popper.update();
                })
            }
        },
        updated() {
            this.$nextTick(()=>{
                this.popper&&this.popper.update();
            })
        },
        mounted() {
            this.$bind = this.$slots.default[0].elm;
            this.$box = this.$refs.box.$el;
            if(this.width === 0) {
                this.$refs.box.$el.style.width = this.$bind.offsetWidth + 'px';
            }else{
                this.$refs.box.$el.style.width = this.width + 'px';
            }
            document.body.appendChild(this.$refs.box.$el);
            this.popper = new Popper(this.$el, this.$refs.box.$el, {
                placement: this.placement
            })
        },
        beforeDestroy() {
            document.body.removeChild(this.$refs.box.$el);
        },
        components: {
            box
        }
    }
</script>



