
<script>
    export default {
        name: 'tabs',
        data(){
            return {
                tabs: [],
                tabActived: null
            }
        },
        mounted() {
            this.tabs = this.$children;
            if(this.tabs.length > 0) {
                this.activeTab(this.tabs[0]);
            }
        },
        methods: {
            activeTab(tab) {
                this.tabActived = tab;
                this.$children.forEach(item=>{
                    item.show = false;
                })
                this.tabActived.show = true;
                this.tabActived.$emit('actived');
                this.$emit('actived', this.tabs.indexOf(this.tabActived));
            }
        },
        render(h){
            return h('div', {class: 'sf-tabs'}, [
                h('ul', {class: 'sf-tabs__tabs'}, [
                    ...this.tabs.map(tab=>h('li', {
                        class: {
                            'sf-tabs__tab': true,
                            'sf-tabs__tab--actived': this.tabActived===tab
                        },
                        on: {
                            click:()=>{
                                this.activeTab(tab);
                            }
                        }
                    }, tab.$slots.title?tab.$slots.title:tab.title))
                ]),
                this.$slots.default
            ])
        }
    }
</script>



