<script>

// TODO: 文字和图表对齐

import pick from 'ramda/src/pick'
import insert from 'ramda/src/insert'
import reduce from 'ramda/src/reduce'
import valuesIn from 'ramda/src/valuesIn'
import clone from 'ramda/src/clone'
import lensProp from 'ramda/src/lensProp'
import set from 'ramda/src/set'
import prop from 'ramda/src/prop'
import contains from 'ramda/src/contains'
import isArray from 'lodash/isArray'
import difference from 'lodash/difference'
import resize from '../../directives/resize'

function track(d) {
    console.log(d);
    return d;
}

export default {
    name: 'datatable',
    props: {
        // 单元格的类型，可选 index, checkbox, radio, normal, template
        data: {
            type: Array,
            default() {
                return []
            }
        },
        hasHeader: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            columnOptions: [],
            // 被选中的条目
            checkboxOption: null,
            detail: null,
            // 排序的字段
            sortField: '',
            // 排序的方法
            sorter: null,
            scrollLeft: 0,
            scrollWidth: 0,
            offsetWidth: 0,
            hoverIndex: -1
        }
    },
    computed: {
        tableTitles() {
            return this.columnOptions.map(item=>item.title);
        },
        // 经过处理的，比如排序，筛选之后的 data
        cookedData() {
            // 对数据进行排序
            var filteredData = this.data.map(item=>item),
                sortedData = filteredData,
                sortCol = this.columnOptions.filter(item=>item.sortable&&item.sortState!=='none')[0],
                filterCols = this.columnOptions.filter(item=>!!item.filter);

            // 做筛选
            filterCols.forEach(col=>{
                filteredData = filteredData.filter(item=>{
                    return col.filter(col.filterValue ,item[col.field], item)
                })
            })

            // 做排序
            sortedData = filteredData;
            if(sortCol) {
                var theProp = prop(sortCol.field);
                sortedData = sortedData.sort((a,b)=>{
                    if(sortCol.sortState === 'asc') {
                        return sortCol.sorter(theProp(a),theProp(b));
                    }else {
                        return sortCol.sorter(theProp(b),theProp(a));
                    }
                })
            }else {
                sortedData = filteredData;
            }
            return sortedData;
        },
        tableBodyData() {
            var fields = this.columnOptions.map(item=>item.field);
            return reduce((a, item)=>{
                // a.push(set(typeLens, 'normal', pick(fields)(item)));
                a.push({
                    type: 'normal',
                    index: this.cookedData.indexOf(item),
                    data: pick(fields)(item)
                })
                if(this.detailHandler&&contains(item, this.detailHandler.expandItems)) {
                    // a.push(set(typeLens, 'detail', clone(item)));
                    a.push({
                        type: 'detail',
                        index: this.cookedData.indexOf(item),
                        data: item
                    })
                }
                return a;
            }, [], this.cookedData);
        },
        allCheckStateClass() {
            var checkableItems = this.cookedData.filter(item=>this.checkboxHandler.condition(item)),
                length = difference(checkableItems, this.checkboxHandler.checkedItems).length;
            if(checkableItems.length === 0) return 'fa-square-o';
            if(length===0) return 'fa-check-square';
            if(length===checkableItems.length) return 'fa-square-o';
            return 'fa-minus-square';
        },
        // 含有左侧固定列
        hasFixedLeft() {
            return this.columnOptions.reduce((a, item)=>{
                if(item.fixed === 'left') {
                    return a||true;
                }else {
                    return a||false;
                }
            } ,false);
        },
        // 含有右侧固定列
        hasFixedRight() {
            return this.columnOptions.reduce((a, item)=>{
                if(item.fixed === 'right') {
                    return a||true;
                }else {
                    return a||false;
                }
            } ,false);
        }
    },
    methods: {
        _if(condition, node) {
            if(condition) return node
            else return ''
        },
        checkAll($event) {
            if(difference(this.cookedData.filter(item=>this.checkboxHandler.condition(item)), this.checkboxHandler.checkedItems).length===0) {
                this.checkboxHandler.checkedItems.splice(0, this.checkboxHandler.checkedItems.length);
            }else {
                this.checkboxHandler.checkedItems.splice(0, this.checkboxHandler.checkedItems.length);
                this.cookedData.forEach(item=>{
                    if(!this.checkboxHandler.condition(item)) return;
                    this.checkboxHandler.checkedItems.push(item);
                })
            }
        },
        checkOne($event, item) {
            var items = this.checkboxHandler.checkedItems;
            if(contains(item)(items)) {
                items.splice(items.indexOf(item),1);
            }else {
                items.push(item);
            }
        },
        // 对数据进行排序，根据字段
        sortByCol(col) {
            // 切换列的排序状态，
            col.changeSortState()
            // 将其他列的排序状态设置为none
            track(this.columnOptions.filter(item=>(!!item.sortState&&item!==col))).forEach(item=>{item.clearSortState()});
        },
        coordinateStyle() {
            var fixedleft = this.$el.querySelector('.sf-datatable__left'),
                fixedright = this.$el.querySelector('.sf-datatable__right'),
                scroll = this.$el.querySelector('.sf-datatable__middle'),
                fixedleftTable = this.$el.querySelector('.sf-datatable__left .sf-datatable__table'),
                fixedrightTable = this.$el.querySelector('.sf-datatable__right .sf-datatable__table'),
                scrollTable = this.$el.querySelector('.sf-datatable__middle .sf-datatable__table'),
                scrollTableThs = Array.prototype.slice.apply(scrollTable.querySelectorAll('.sf-datatable-thead th'));

            if(this.hasFixedLeft) {
                fixedleftTable.style.width = (scrollTable.offsetWidth + 'px');
                var leftCount = this.columnOptions.reduce((a, item)=>{
                    if(item.fixed === 'left') a++;
                    return a;
                }, 0);
                var leftWidth = scrollTableThs.slice(0, leftCount).reduce((a, item)=>{
                    return a+item.offsetWidth;
                },0)
                fixedleft.style.width = `${leftWidth}px`;
            }

            if(this.hasFixedRight) {
                fixedrightTable.style.width = (scrollTable.offsetWidth + 'px');
                var rightCount = this.columnOptions.reduce((a, item)=>{
                    if(item.fixed === 'right') a++;
                    return a;
                }, 0);

                var rightWidth = scrollTableThs.slice(-1*rightCount).reduce((a, item)=>{
                    return a+item.offsetWidth;
                },0);

                fixedright.style.width = `${rightWidth}px`;
            }

        },
        updateScroll() {
            this.scrollLeft = this.$el.querySelector('.sf-datatable__middle').scrollLeft;
            this.scrollWidth = this.$el.querySelector('.sf-datatable__middle').scrollWidth;
            this.offsetWidth = this.$el.querySelector('.sf-datatable__middle').offsetWidth;
        },
        resizeHandler(e) {
            this.coordinateStyle();
            this.updateScroll();
        },
        mouseoverHandler(e) {
            var tr = e.target.closest('.sf-datatable__row');
            if(tr === null) return;
            var trs = Array.prototype.slice.apply(tr.parentElement.children);
            this.hoverIndex = trs.indexOf(tr);
        },
        mouseleaveHandler(e) {
            this.hoverIndex = -1;
        },
        renderCol(_h, columnOptions) {
            var vm = this;
            return _h('table', {class:['sf-datatable__table']}, [
                this._if(this.hasHeader, _h('thead', {class:['sf-datatable-thead']} , [_h('tr',[
                    ...columnOptions.map((option, index)=>_h('th',{
                        style: {
                            minWidth: `${option.width}px`
                        }
                    },[
                        option.type==='normal'?_h('span',[
                            option.title,
                            option.sortable?_h('span', {
                                class: ['fs-table__sort-btn', 'fs-table__sort-btn--'+option.sortState],
                                on: {
                                    click: function($event) {
                                        vm.sortByCol(option);
                                    }
                                }
                            }, [
                                _h('i', {class: ['fa', 'fa-caret-up']}),
                                _h('i', {class: ['fa', 'fa-caret-down']}),
                            ]):''
                        ]):'',
                        option.type==='checkbox'?_h('i',{
                            class: ['sf-datatable__checkbox', 'fa', vm.allCheckStateClass],
                            on:{
                                click:function($event){vm.checkAll($event)}
                            },
                            attrs: {
                                disabled: vm.cookedData.every(item=>!vm.checkboxHandler.condition(item))
                            }
                        }):'',
                        option.type==='detail'?'':''
                    ]))
                ])])),
                _h('tbody',[
                    ...this.tableBodyData.map((item, trIndex)=>item.type==='normal'?_h('tr',{
                        class: [
                            trIndex===this.hoverIndex?'hover':'',
                            'sf-datatable__row'
                        ]
                    },[
                        ...columnOptions.map((option, index)=>_h('td', [
                            option.type==='normal'?option.cellRender({value: item.data[option.field]||'', item: vm.cookedData[item.index], index: item.index}):'',
                            option.type==='checkbox'?_h('i',{
                                on:{
                                    click:function($event){
                                        vm.checkOne($event, vm.cookedData[item.index])
                                    }
                                },
                                domProps: {
                                    checked: contains(vm.cookedData[item.index])(vm.checkboxHandler.checkedItems)
                                },
                                class: ['sf-datatable__checkbox', 'fa', contains(vm.cookedData[item.index])(vm.checkboxHandler.checkedItems)?'fa-check-square':'fa-square-o'],
                                attrs: {
                                    type:'checkbox',
                                    disabled: !vm.checkboxHandler.condition(vm.cookedData[item.index])
                                }
                            }):'',
                            option.type==='detail'?_h('i', {
                                class: ['sf-datatable__detail-btn', 'fa', contains(vm.cookedData[item.index])(vm.detailHandler.expandItems)?'fa-minus-square-o':'fa-plus-square-o'],
                                on: {
                                    click: function($event) {
                                        if(contains(vm.cookedData[item.index])(vm.detailHandler.expandItems)) {

                                            // 如果已经展开
                                            vm.detailHandler.expandItems.splice(vm.detailHandler.expandItems.indexOf(vm.cookedData[item.index]), 1);
                                        }else {
                                            // 如果没有展开
                                            vm.detailHandler.expandItems.push(vm.cookedData[item.index]);
                                        }
                                    }
                                }
                            }):''
                        ]))
                    ]):_h('tr',{
                        class: ['sf-datatable__row']
                    },[
                        _h('td',{
                            attrs: {
                                colspan: columnOptions.length
                            }
                        },[
                            this.detailHandler.detailRender(this.cookedData[item.index])
                        ])
                    ]))
                ]),
                ...this.$slots.default
            ])
        }
    },
    mounted() {
        // 获取列的选项
        this.columnOptions = this.$children.filter(item=>item.$options.name.match(/^datatable-/));
        // 获取选中数据 为保持联动，不要改变 checkedItems本身
        this.checkboxHandler = this.$children.filter(item=>item.type==='checkbox')[0];
        // 获取详情处理句柄
        this.detailHandler = this.$children.filter(item=>item.type==='detail')[0];

        window.addEventListener('resize', this.resizeHandler);
        this.$el.querySelector('.sf-datatable__middle').addEventListener('scroll', this.updateScroll)
        this.$el.addEventListener('mouseover', this.mouseoverHandler)
        this.$el.addEventListener('mouseleave', this.mouseleaveHandler)
    },
    updated() {
        this.updateScroll();
        this.coordinateStyle();
    },
    beforeDestory() {
        window.removeEventListener('resize', this.resizeHandler);
        this.$el.querySelector('.sf-datatable__middle').removeEventListener('scroll', this.updateScroll)
        this.$el.removeEventListener('mouseover', this.mouseoverHandler)
        this.$el.removeEventListener('mouseleave', this.mouseleaveHandler)
    },
    render(_h) {
        return _h('div', {class: ['sf-datatable']}, [
            // 固定在左侧的列
            _h('div', {
                class: ['sf-datatable__left', this.scrollLeft===0?'':'sf-datatable__left--shadow'],
                style: {
                    display: this.hasFixedLeft?'block':'none'
                }
            }, [
                this.renderCol(_h, this.columnOptions)
            ]),
            // 可横向滚动的列
            _h('div', {class: ['sf-datatable__middle']}, [
                this.renderCol(_h, this.columnOptions)
            ]),
            // 固定在右侧的列
            _h('div', {
                class: ['sf-datatable__right', this.scrollLeft+this.offsetWidth===this.scrollWidth?'':'sf-datatable__right--shadow'],
                style: {
                    display: this.hasFixedRight?'block':'none'
                }
            }, [
                this.renderCol(_h, this.columnOptions)
            ])
        ])
    }
}
</script>



