<template lang="html">
    <span class="sf-preview" >
        <span @click="open" class="sf-preview__wrap">
            <slot></slot>
        </span>
        <div
            :class="{
                'sf-preview__displayer':true,
                'sf-preview__displayer--single': group.length===1
            }"
            v-if="showing"
        >

            <iframe
                v-if="current.type==='pdf'"
                :src="current.src"
                class="sf-preview__area"
            ></iframe>
            <scale-area
                v-else
                class="sf-preview__area"
                ref="area"
            >
                <img :src="current.src">
            </scale-area>

            <div class="sf-preview__toolbar" v-if="current.type !== 'pdf'">
                <div class="sf-preview__tool" @click="$refs.area.scaleIt(0.2)">
                    <i class="fa fa-plus"></i>
                </div>
                <div class="sf-preview__tool" @click="$refs.area.scaleIt(-0.2)">
                    <i class="fa fa-minus"></i>
                </div>
                <div class="sf-preview__tool" @click="$refs.area.roateIt(-90)">
                    <i class="fa fa-rotate-left"></i>
                </div>
                <div class="sf-preview__tool" @click="$refs.area.roateIt(90)">
                    <i class="fa fa-rotate-right"></i>
                </div>
            </div>

            <i class="sf-preview__close fa fa-times" @click="close"></i>
            <ul class="sf-preview__group">
                <li
                    v-for="item in group"
                    :class="{
                        'sf-preview__group-item': true,
                        'sf-preview__group-item--active': item === current
                    }"
                    @click="select(item)"
                >
                    <i class="sf-preview__thumb sf-preview__thumb--pdf sf-preview__thumb fa fa-file-pdf-o" v-if="item.type === 'pdf'"></i>
                    <img class="sf-preview__thumb sf-preview__thumb--img" :src="item.src" v-else>
                </li>
            </ul>
        </div>
    </span>
</template>
<script>

    // TODO: 图片预览 边界限制
    // TODO: 图片预览 旋转， 归位(增加工具条)
    // TODO: DOC EXCEL 预览
    // TODO: 样式调整

    import Vue from 'vue'
    import scaleArea from './scale-area'
    import detector from '../../utils/detector.js'

    // 组管理器
    var GroupMng = Vue.extend({
        data() {
            return {
                groups: {}
            }
        },
        methods: {
            // 添加一个项目
            add(item) {
                var groupId = item.groupId;
                if(this.hasGroup(groupId)) {
                    this.addToGroup(groupId, item);
                }else {
                    this.createGroup(groupId);
                    this.addToGroup(groupId, item);
                }
            },
            // 删除一个项目
            remove(item) {
                var groupId = item.groupId;
                if(!this.hasGroup(groupId)) return;
                var group = this.groups[groupId];
                group.splice(group.indexOf(item),1);
                if(group.length === 0);
                delete this.groups[groupId];
            },
            // 添加到组
            addToGroup(groupId, item) {
                if(!this.hasGroup(groupId)) return ;
                this.groups[groupId].push(item);
            },
            // 是否存在组
            hasGroup(groupId) {
                return this.groups[groupId]?true:false;
            },
            createGroup(groupId) {
                this.groups[groupId] = [];
            },
            getGroup(groupId) {
                return this.groups[groupId]||null;
            }
        }
    });

    var groupMng = new GroupMng();

    // TODO: esc
    // TODO: group ✅
    // TODO: 预览功能
    // TODO: 图片懒加载

    export default {
        name: 'preview',
        props:{
            // 资源地址
            src: '',
            // 组编号 默认分配一个随机数
            groupId: {
                type: String,
                default: function() {
                    return Math.random().toString()
                }
            }
        },
        data() {
            return {
                // 是否显示
                showing: false,
                // 当前显示的项目
                current: this,
                // 类型
                type: ''
            }
        },
        created: async function() {
            groupMng.add(this);

            // 检测资源的类型
            this.type = await detector(this.src);

        },
        computed: {
            group() {
                return groupMng.getGroup(this.groupId);
            }
        },
        methods: {
            open() {
                this.showing = true;
                this.current = this;
            },
            close() {
                this.showing = false;
            },
            select(item) {
                this.current = item;
                if(this.$refs.area) this.$refs.area.reset();
            }
        },
        components: {
            scaleArea
        },
        mounted() {
            window.addEventListener('keydown', event => {
                if(event.keyCode === 27 && this.showing) {
                    this.close();
                }
            });
        }
    }
</script>



