<template lang="html">
    <div :style="{
            'width': width,
            'align-self': alignSelf,
            'left': relative_ ,
            'margin': margin,
            'padding': padding,
            'flex': flex
        }"
        v-offset="{
            offsetWidth: offsetWidth,
            padding: padding,
            offsetFlex: offsetFlex,
            offset:offset,
            isLengthPos: isLengthPos
        }"
        class="sf-col"
    >
        <slot></slot>
    </div>
</template>

<script>

import toLengthAbs from '../../utils/toLengthAbs'
import toLength from '../../utils/toLength'
import isLengthPos from '../../utils/isLengthPos'
import extend from 'lodash/extend'
import isNumber from 'lodash/isNumber'
import isString from 'lodash/isString'
import includes from 'lodash/includes'
var valignVSelf = ['top', 'bottom', 'center', 'justify', 'stretch']
var valign2alignSelf= {
    left: 'flex-start',
    right: 'flex-end',
    center: 'center',
    stretch: 'stretch'
}

export default {
    name: 'col',
    props: {
        /**
         * 每个块的尺寸
         * 可选：
         * 1px 像素长度
         * 10% 百分比长度
         * 1/2 转化为百分比长度
         * 0.5 转化为百分比长度
         * 1 2 3 转化为 flex 长度分配
         * auto 转化为 flex=1
         */
        span: {
            type: [String,Number],
            default: 'initial',
        },
        // 左侧的偏移
        offset: {
            type: [String,Number],
            default: 'initial'
        },
        // 相对位移
        relative: {
            type: String,
            default: '0'
        },
        // 垂直对其方式
        valign: {
            type: String,
            default: 'top',
            validator: value=>{
                return includes(valignVSelf, value);
            }
        }
    },
    data: function () {
        return {
            rowWidth: 0
        }
    },
    created: function() {
    },
    computed: {
        relative_() {
            return toLength(this.relative);
        },
        gutter() {
            return this.$parent.gutter;
        },
        alignSelf() {
            return valign2alignSelf[this.valign];
        },
        margin() {
            return '0 0 0 0';
        },
        // 用来实现 gutter
        padding() {
            return '0 '+(parseFloat(this.gutter)/2)+'px';
        },
        // 如果span为大于等于1的数字，则可以转化为 flex，或者为auto转为flex=1
        flex() {
            if(isNumber(this.span) && this.span >= 1) {
                return this.span.toString();
            }
            if(isString(this.span) && this.span === 'stretch') {
                return '1';
            }
            return 'none';
        },
        width() {
            return toLength(this.span);
        },
        // 如果span为大于等于1的数字，则可以转化为 flex，或者为auto转为flex=1
        offsetFlex() {
            if(isNumber(this.offset) && this.offset >= 1) {
                return this.offset.toString();
            }
            if(isString(this.offset) && this.offset === 'stretch') {
                return '1';
            }
            return 'none';
        },
        offsetWidth() {
            return toLengthAbs(this.offset);
        },
        isLengthPos() {
            return isLengthPos(this.offset);
        }
    },
    directives: {
        'offset': {
            inserted(el, binding) {
                var value = binding.value;
                // 添加到文档中
                if(value.offset === 'initial') return;
                // 创建元素
                var offsetEl = document.createElement('div')
                extend(offsetEl.style, {
                    'width': value.offsetWidth,
                    'padding': value.padding,
                    'flex': value.offsetFlex
                })
                if(value.isLengthPos) {
                    // 在左边插入
                    el.parentNode.insertBefore(offsetEl, el);
                }else {
                    // 在右边插入
                    el.parentNode.insertBefore(offsetEl, el.nextSibling);
                }
            }
        }
    }
}
</script>



