<script>
import expand from '../expand/expand.vue'
import inputBox from '../input-box/input-box.vue'
import calendar from './calendar.vue'
export default {
    name: 'datepicker',
    props: {
        value: {
            default: ''
        },
        startDate: {
            default: '1900-1-1'
        },
        endDate: {
            default: '2099-12-31'
        },
        rangeTo: {
            default: ''
        },
        rangeFrom: {
            default: ''
        },
        // 将value转化为 date的处理函数
        toDate: {
            type: Function,
            default(value) {
                return new Date(value);
            }
        },
        // 将date对象转化为 value 对象
        format: {
            type: Function,
            default(date) {
                var dateStr = date.getFullYear() + '-' + (date.getMonth() + 1)
                dateStr += '-' + (date.getDate())
                return dateStr;
            }
        }
    },
    data() {
        return {
            show: false
        }
    },
    components: {
        calendar,
        expand,
        'input-box': inputBox
    },
    mounted() {
        window.addEventListener('click', this.clickOther)
    },
    destroyed() {
        window.removeEventListener('click', this.clickOther)
    },
    methods: {
        clickOther() {
            if(this.$el.contains(event.target)) {
                return ;
            }
            if(this.$refs.expand.$box.contains(event.target)) {
                return;
            }
            this.show = false;
        }
    },
    render(h) {
        return h('div', {class: ['sf-select-datepicker']}, [
            h('expand',  {
                props: {
                    show: this.show,
                    width: 229
                },
                ref: 'expand'
            },[
                h('input-box', {
                    props: {
                        value: this.value,
                        editable: false
                    },
                    ref:"inputBox",
                    on: {
                        focus: () => {
                            this.show = true;
                        },
                        esc: event=>{
                            this.show = false;
                        },
                        tab: event=> {
                            setTimeout(()=>{
                                if(!this.$refs.inputBox.$el.contains(document.activeElement)) {
                                    this.show = false;
                                }
                            })
                        }
                    }
                },[
                    h('span', [this.value])
                ]),
                h('div',{slot: 'content'},[
                    h('calendar', {
                        props: {
                            currentDate: this.rangeFrom?[this.toDate(this.rangeFrom), this.toDate(this.value)]:[this.toDate(this.value)],
                            startDate: this.rangeFrom?this.toDate(this.rangeFrom):this.toDate(this.startDate),
                            endDate: this.rangeTo?this.toDate(this.rangeTo):this.toDate(this.endDate)
                        },
                        on: {
                            input: (date)=>{
                                this.$emit('input', this.format(date));
                                this.show = false;
                            },
                            // click: () => {
                            //     this.$refs.inputBox.focus();
                            // }
                        }
                    })
                ])
            ])
        ])
    }
}
</script>



