// TODO: hover 效果
export default {
    name: 'table-shell',
    props: {
        // 固定在左侧的列数
        fixLeft: {
            type: Number,
            default: 0
        },
        // 固定在右侧的列数
        fixRight: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            overflowLeft: false,
            overflowRight: false
        }
    },
    methods: {
        updatePosition() {
            this.$left.style.width = this.$ths.slice(0, this.fixLeft).reduce((x, item)=>{
                return x+=item.offsetWidth;
            }, 0)+'px';
            this.$right.style.width = this.$ths.slice(this.$ths.length-this.fixRight).reduce((x, item)=>{
                return x += item.offsetWidth;
            }, 0)+'px';
            this.$right.querySelector('table').style.width = `${this.$table.clientWidth}px`;
        },
        updateShadow() {
            const scrollLeft = this.$middle.scrollLeft
            const offsetWidth = this.$middle.offsetWidth
            const scrollWidth = this.$middle.scrollWidth

            this.overflowLeft = (scrollLeft !== 0)
            this.overflowRight = (scrollLeft + offsetWidth !== scrollWidth)
        }
    },
    updated() {
        this.$nextTick(()=>{
            this.updatePosition();
            this.updateShadow();
        })
    },
    mounted() {
        this.$left = this.$el.querySelector('.table-shell__left');
        this.$right = this.$el.querySelector('.table-shell__right');
        this.$middle = this.$el.querySelector('.table-shell__middle');
        this.$table = this.$middle.querySelector('table');
        this.$ths = Array.from(this.$table.querySelectorAll('th')||[]);


        this.$middle.addEventListener('scroll', this.updateShadow);
        window.addEventListener('resize', this.updateShadow);
        window.addEventListener('resize', this.updatePosition);
        this.updatePosition();
        this.updateShadow();
    },
    destroy() {
        this.$middle.removeEventListener('scroll', this.updateShadow);
        window.removeEventListener('resize', this.updateShadow);
        window.removeEventListener('resize', this.updatePosition);
    },
    render(h) {
        return <div class="table-shell">
            <div class="table-shell__left" v-show={this.overflowLeft}>
                { this.$scopedSlots.default() }
            </div>
            <div class="table-shell__middle">
                { this.$scopedSlots.default() }
            </div>
            <div class="table-shell__right" v-show={this.overflowRight}>
                { this.$scopedSlots.default() }
            </div>
        </div>
    }
}



