<template lang="html">
    <div class="sf-mv100 sf-mh15 sf-tc" style="color: #999;" v-show="!!status">
        <div v-if="status==='error'">
            <i class="fa fa-ban" style="font-size: 100px;"></i>
            <br>
            <br>
            <p style="font-size: 2rem;">请求接口出错,请尝试 <a href="#" @click.prevent="handler">刷新</a></p>
        </div>
        <div v-if="status==='empty'">
            <i class="fa fa-television" style="font-size: 100px;"></i>
            <br>
            <br>
            <p style="font-size: 2rem;">没有数据</p>
        </div>
        <div v-if="status==='loading'">
            <i class="fa fa-spinner" style="font-size: 100px;"></i>
            <br>
            <br>
            <p style="font-size: 2rem;">数据加载中</p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'panel-alert',
    props: {
        // 显示的状态
        // error
        // empty
        // loading
        status: {
            type: String,
            default: ''
        }
    },
    methods: {
        handler() {
            location.reload();
        }
    }
}
</script>



