<template lang="html">
    <div class="sf-number" :class="{'sf-number--readonly': readonly}">
        <input
            type="text"
            class="sf-number__input"
            :readonly="readonly"
            :disabled="disabled"
            :placeholder="placeholder"
            :value="value"
            @input="$emit('input', $event.target.value)"
            @blur="correct()"
        >
        <div class="sf-number__panel panel">
            <div class="sf-number__btn" @click="add()">
                <i class="fa fa-caret-up"></i>
            </div>
            <div class="sf-number__btn" @click="subtract()">
                <i class="fa fa-caret-down"></i>
            </div>
        </div>
    </div>
</template>

<script>

    import isNumber from 'lodash/isNumber'
    import isNaN from 'lodash/isNaN'
    import isUndefined from 'lodash/isUndefined'
    import round from 'lodash/round'
    import input from '../../mixins/input.vue'
    export default {
        name:'number',
        mixins:[input],
        props: {
            // 最大值
            max: {
                type: Number,
                default: Infinity
            },
            // 最小值
            min: {
                type: Number,
                default: -Infinity
            },
            // 每次切换的步长
            step: {
                type: Number,
                default: 1
            },
            readonly: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            number: {
                set(value) {
                    if(!isNumber(value)) return;
                    var n = this.step.toString().split('.')[0].length;
                    this.$emit('input', round(value, n));
                },
                get() {
                    return parseFloat(this.value);
                }
            }
        },
        methods: {
            // 去除小数的尾巴
            _clearTail(value){
                var n = this.step.toString().split('.')[0].length;
                var valuesp = value.split('.');
                if(isUndefined(valuesp[1])) {
                    valuesp[1] = (function(){
                        var v = '';
                        while(n--) {
                            v += '0';
                        }
                        return v;
                    })();
                }else if(valuesp[1].length > n) {
                    // TODO:
                    valuesp[1] = valuesp.slice(0,n);
                }else if(valuesp[1].length < n) {
                    var i = n - valuesp[1].length;
                    valuesp[1] = (function(){
                        var v = '';
                        while(i--) {
                            v += '0';
                        }
                        return v;
                    })();
                }
                return valuesp.join('.');
            },
            add() {
                if(this.readonly) return;
                if(isNaN(this.number)) {
                    this.number = this.step;
                }else {
                    if(this.number + this.step > this.max) return;
                    this.number += this.step;
                }
            },
            subtract() {
                if(this.readonly) return;
                if(isNaN(this.number)) {
                    this.number = this.min;
                }else {
                    if(this.number - this.step < this.min) return;
                    this.number -= this.step;
                }
            },
            // 校准value数据
            correct() {
                if(this.value === '') return;
                // 如果value不能解析成数字，则设置为空
                if(isNaN(parseFloat(this.value))) {
                    this.$emit('input', '');
                    return;
                }
                // 判断是否超过边界，如果超过，则设置为最大或者最小值
                if(this.number > this.max) {
                    this.number = this.max;
                    return;
                }
                if(this.number< this.min) {
                    this.number = this.min;
                    return;
                }
                // 将value设置为step的倍数
                this.number = (parseInt(this.number / this.step)*this.step);
            }
        }
    }
</script>

<style lang="less">
    .sf-number {
        box-sizing: border-box;
        position: relative;
        display: inline-block;
    }
    input[type=text].sf-number__input {
        line-height: 1.5em;
        padding: 0.25em 0.5em;
        border: none;
        border: 1px solid #ccc;
        width: 100%;
    }
    input[type=text].sf-number__input:focus {
        outline: none;
        outline: 1px solid #ccc;
    }


    .sf-number__panel {
        display: none;
        width: 30px;
        position: absolute;
        height: 100%;
        right: 0px;
        top: 0px;
        border-left: 1px solid #ccc;
        font-size: 10px;
        color: #666;
        text-align: center;
        line-height: 14px;
    }

    .sf-number__btn {
        cursor: pointer;
        height: 50%;
        box-sizing: border-box;
        i {
            height: 100%;
            vertical-align: middle;
            line-height: 1em;
        }
    }
    .sf-number__btn:first-child {
        border-bottom: 1px solid #ccc;
    }
    .sf-number__btn:active {
        background-color: #ccc;
    }

    .sf-number--readonly .sf-number__btn:active {
        background-color: inherit;
    }

    // hover的时候显示面板
    .sf-number .sf-number__panel{
        display: block;
    }
</style>



