<template lang="html">
    <div :style="{
            position: position_,
            left: left_,
            right: right_,
            top: top_,
            bottom: bottom_,
            'z-index': zIndex,
            'margin-left': marginLeft_,
            'margin-top': marginTop_
        }"
        class="sf-lay"
    >
        <slot></slot>
    </div>
</template>

<script>
    import includes from 'lodash/includes'
    import toLength from '../../utils/toLength'
    export default {
        name: 'lay',
        props: {
            // 放置的位置 ,有两种可以选择， parent 和 viewport
            // 如果放在parent 相当于绝对定位，如果放在viewport相当于固定定位
            where: {
                type: String,
                default: 'parent',
                validator(val) {
                    if(includes(['parent', 'viewport'], val)) {
                        return true;
                    }else {
                        return false;
                    }
                }
            },
            left: {
                type: String,
                default: 'initial',
                coerce: toLength
            },
            right: {
                type: String,
                default: 'initial',
                coerce: toLength
            },
            top: {
                type: String,
                default: 'initial',
                coerce: toLength
            },
            bottom: {
                type: String,
                default: 'initial',
                coerce: toLength
            },
            zIndex: {
                type: [String, Number],
                default: '1'
            },
            // 水平居中
            vcenter: {
                type: Boolean,
                default: false
            },
            // 垂直居中
            hcenter: {
                type: Boolean,
                default: false
            },
            // 贴在某一个地方，可选值， all, left, right ,bottom ,top。比如贴在left， 则高度为100%，left=0
            paste: {
                type: String,
                default: '',
                validator(val) {
                    if(includes(['all', 'left', 'right', 'bottom', 'top'], val)) {
                        return true;
                    } else {
                        return false;
                    }
                }
            }
        },
        watch: {
            left() {
                this.layit();
            },
            right() {
                this.layit();
            },
            top() {
                this.layit();
            },
            bottom() {
                this.layit();
            },
            vcenter() {
                this.layit();
            },
            hcenter() {
                this.layit();
            },
            paste() {
                this.layit();
            }
        },
        computed: {
            position_() {
                if(this.where === 'parent') {
                    return 'absolute';
                }else {
                    return 'fixed';
                }
            }
        },
        data() {
            return {
                left_: 'initial',
                right_: 'initial',
                bottom_: 'initial',
                top_: 'initial',
                marginLeft_: 'initial',
                marginTop_: 'initial'
            }
        },
        methods: {
            layit() {
                this.left_ = this.left;
                this.right_ = this.right;
                this.top_ = this.top;
                this.bottom_ = this.bottom;

                if(this.paste === 'all') {
                    this.left_ = '0';
                    this.right_ = '0';
                    this.top_ = '0';
                    this.bottom_ = '0';
                }else if(this.paste === 'top') {
                    this.left_ = '0';
                    this.right_ = '0';
                    this.top_ = '0';
                }else  if(this.paste === 'bottom') {
                    this.left_ = '0';
                    this.right_ = '0';
                    this.bottom_ = '0';
                }else if(this.paste === 'left') {
                    this.left_ = '0';
                    this.top_ = '0';
                    this.bottom_ = '0';
                } else if(this.paste === 'right') {
                    this.right_ = '0';
                    this.top_ = '0';
                    this.bottom_ = '0';
                }

                if(this.vcenter) {
                    this.top_ = '50%';
                }
                if(this.hcenter) {
                    this.left_ = '50%';
                }
            }
        },
        created() {
            this.layit();
        },
        ready() {
            // 获取长度计算负边距
            if(this.vcenter) {
                this.marginTop_ = -1*this.$el.offsetHeight/2+'px';
            }
            if(this.hcenter) {
                this.marginLeft_ = -1*this.$el.offsetWidth/2+'px';
            }
        }
    }
</script>



