<template>
    <div class="calendar-panel" :style="{width: width + 'px'}" @click="$emit('click')">
        <div>
            <div class="calendar-container">
                <div v-for="calendar in calendars" class="calendar-wrapper" :style="{width: width + 'px'}">
                    <h6><span>{{ calendar.year }}年{{ calendar.month }}月</span></h6>
                    <div>
                        <table>
                            <tbody>
                                <tr class="header">
                                    <th v-for="header in headers" :class="{'sunday': header.day === 0, 'saturday': header.day === 6}">{{ header.str }}</th>
                                </tr>
                                <tr v-for="lineDates in calendar.dates">
                                    <td v-for="date in lineDates" :class="date.classList" @mouseenter="enter(date)" @mouseleave="out(date, $event)" @click="pick(date)"><span class="d">{{ date.dateName || date.day }}</span></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div v-if="needClear === true" class="control-container">
                <button v-if="needClear === true" class="clear-control">清空</button>
            </div>
            <div class="month-nav">
                <span class="previous-month-panel" @click="previous()"><i class="previous-month"></i></span>
                <span class="next-month-panel" @click="next()"><i class="next-month"></i></span>
            </div>
        </div>
        <i v-if="direction" :class="direction"><em></em></i>
    </div>
</template>

<script>
/**
 * chrome性能监测，内存使用情况
 * chrome的vue开发公主
 *
**/
export default {
    name: 'calendar',
    props: {
        direction: {  // 作为日期选择时，日历面板的方位
            type: String,
            default: ''
        },
        width: {
            type: Number,
            default: 226 // 226，large皮肤下是265
        },
        showNum: {  // 出现几个选择区域，默认是1
            type: Number,
            default: 1
        },
        firstDay: {  // 第一排是星期几，0表示周日，1表示周一，依次类推，默认是周日
            type: Number,
            default: 0
        },
        showSideMonth: {  // 是否显示上下月份，默认不显示
            type: Boolean,
            default: false
        },
        // @TODO 暂未实现
        role: {  // 用途，'month'年月选择, 'minute'时分选择, 'second'时分秒选择，
            type: String
        },
        // @TODO 只实现了rangeFrom和rangeTo，range和multi待实现
        mode: {  // 模式，是选择起始日期（'rangeFrom'），还是截止日期（'rangeTo'）,还是区间选择（'range'），多日期选择（multi，离散选择一些日期）
            type: String,
            default: 'rangeFrom'
        },
        // @TODO 暂未实现
        hourStep: {  // 小时的步长（几乎用不到），role为'minute'和'second'有效
            type: Number,
            default: 1
        },
        // @TODO 暂未实现
        minuteStep: {  // 分钟的步长，role为'minute'和'second'有效
            type: Number,
            default: 5
        },
        // @TODO 暂未实现
        secondStep: {  // 秒的步长，role为'minute'和'second'有效
            type: Number,
            default: 5
        },
        needClear: {  // 是否显示清除按钮，默认显示
            type: Boolean,
            default: false
        },
        currentDate: {
            type: [String, Date, Array]
        },
        startDate: {  // 起始时间，默认是1900-1-1
            type: [String, Date]
        },
        endDate: {  // 截至时间，默认是2099-12-31
            type: [String, Date]
        },
        defaultToday: {  // 'rangeFrom'模式，如果没有设置currentDate值，如果今天可选的，默认今天选中
            type: Boolean,
            default: true
        }
    },
    data () {
        return {
            current: [],
            selected: [], // 与current对应的选中的日历单元格,
            calendars: [],
            hourList: [],
            festivalMap: {
                '1-1': '元旦',
                '2-14': '情人',
                '5-1': '五一',
                '6-1': '儿童',
                '10-1': '国庆',
                '12-25': '圣诞'
            },
            festivalStorage: {  // 农历和清明节日存储
                // 2021
                '2021-2-11': '除夕',
                '2021-2-12': '春节',
                '2021-2-16': '元宵',
                '2021-4-4': '清明',
                '2021-6-14': '端午',
                '2021-8-14': '七夕',
                '2021-9-21': '中秋',
                // 2020
                '2020-1-24': '除夕',
                '2020-1-25': '春节',
                '2020-2-8': '元宵',
                '2020-4-4': '清明',
                '2020-6-25': '端午',
                '2020-8-25': '七夕',
                '2020-10-1': '中秋',
                // 2019
                '2019-2-4': '除夕',
                '2019-2-5': '春节',
                '2019-2-19': '元宵',
                '2019-4-5': '清明',
                '2019-6-7': '端午',
                '2019-8-7': '七夕',
                '2019-9-13': '中秋',
                // 2018
                '2018-2-15': '除夕',
                '2018-2-16': '春节',
                '2018-3-2': '元宵',
                '2018-4-5': '清明',
                '2018-6-18': '端午',
                '2018-8-17': '七夕',
                '2018-9-24': '中秋',
                // 2017
                '2017-1-27': '除夕',
                '2017-1-28': '春节',
                '2017-2-11': '元宵',
                '2017-4-4': '清明',
                '2017-5-30': '端午',
                '2017-8-28': '七夕',
                '2017-10-4': '中秋',
                // 2016
                '2016-2-7': '除夕',
                '2016-2-8': '春节',
                '2016-2-22': '元宵',
                '2016-4-4': '清明',
                '2016-6-9': '端午',
                '2016-8-9': '七夕',
                '2016-9-15': '中秋',
                // 2015
                '2015-2-18': '除夕',
                '2015-2-19': '春节',
                '2015-3-5': '元宵',
                '2015-4-5': '清明',
                '2015-6-20': '端午',
                '2015-8-20': '七夕',
                '2015-9-27': '中秋',
                // 2014
                '2014-1-30': '除夕',
                '2014-1-31': '春节',
                '2014-2-14': '元宵',
                '2014-4-5': '清明',
                '2014-6-2': '端午',
                '2014-8-2': '七夕',
                '2014-9-8': '中秋',
                // 2013
                '2013-2-9': '除夕',
                '2013-2-10': '春节',
                '2013-2-24': '元宵',
                '2013-4-4': '清明',
                '2013-6-12': '端午',
                '2013-8-13': '七夕',
                '2013-9-19': '中秋'
            }
        }
    },
    watch: {
        startDate() {
            this.init();
            this.update();
        },
        endDate() {
            this.init();
            this.update();
        }
    },
    methods: {
        /**
         * @TODO
         * 内存消耗很大，一定要优化
         * 不是默认格式的日期怎么处理
         * 时间选择的功能
         * 切换动画的实现
         * 默认今天选中，可以关闭，异步日历不支持这个功能
         * 异步日历，不支持showOtherMonth的前后月份的异步
         * 开始时间和结束时间
         * 类名是预先生成好，还是在模板中通过方法生成，预先生成好，处理比价复杂
         * 位置计算的问题
        **/
        // 打开是
        isDate: function (val) {
            return Object.prototype.toString.call(val) === '[object Date]'
        },
        isArray: function (val) {
            return Object.prototype.toString.call(val) === '[object Array]'
        },
        stringToDate: function (str) {  // 只支持格式：1990-01-01 12:00:00
            str = str.trim().split(/[-\s:]/)
            var date = new Date(parseInt(str[0], 10), parseInt(str[1], 10) - 1, str[2] ? parseInt(str[2], 10) : 1)
            if (isNaN(date.getTime())) {
                return
            }
            var methods = ['setHours', 'setMinutes', 'setSeconds']
            for (var i = 3, len = str.length; i < len; i++) {
                date[methods[i - 3]](parseInt(str[i], 10))
            }
            if (isNaN(date.getTime())) {
                return
            }
            return date
        },
        pullZero: function (num) {
            return (num < 10 ? '0' : '') + num
        },
        dateToString: function (date, time) { // 只支持格式：1990-01-01 12:00:00
            var dateStr = date.getFullYear() + '-' + this.pullZero(date.getMonth() + 1)
            if (time === 'month') {
                return dateStr
            }
            dateStr += '-' + this.pullZero(date.getDate())
            if (time !== 'minute' && time !== 'second') {
                return dateStr
            }
            dateStr += this.pullZero(date.getHours()) + ':' + this.pullZero(date.getMinutes())
            if (time === 'minute') {
                dateStr += ':' + this.pullZero(date.getSeconds())
            }
            return dateStr
        },
        createDate: function (date) {  // 只保留时间
            return new Date(date.getFullYear(), date.getMonth(), date.getDate())
        },
        compareDate: function (date1, date2) {
            date1 = this.createDate(date1)
            date2 = this.createDate(date2)
            return date1.getTime() - date2.getTime()
        },
        formatDate: function (date) {
            if (typeof date === 'string') {
                date = this.stringToDate(date)
            }
            if (!this.isDate(date) || isNaN(date.getTime())) {
                return
            }
            return date
        },
        init: function () {
            this.today = new Date()  // 每次open的时候实时更新this.today
            this.start = this.formatDate(this.startDate)
            if (!this.start) {
                this.start = new Date(this.today.getFullYear() - 120, 0, 1) // 默认前120年
            }
            this.end = this.formatDate(this.endDate)
            if (!this.end) {
                this.end = new Date(this.today.getFullYear() + 120, 12, 31)  // 默认后120年
            }
            if (this.isArray(this.currentDate)) {
                for (let i = 0, len = this.currentDate.length; i < len; i++) {
                    this.current[i] = this.formatDate(this.currentDate[i])
                }
            } else {
                var current = this.formatDate(this.currentDate)
                if (current) {
                    if (this.mode === 'rangeTo') {
                        this.current[1] = current
                    } else {
                        this.current[0] = current
                    }
                }
            }
            // showDate 显示的基准日期，日期的月份为第一个面板显示的月份，这个日期的设置顺序current => today => start/end
            if (this.mode === 'rangeTo' && this.current[1]) {  // 选结束区间日期
                if (this.showNum > 1 && this.current[0]) {
                    var date1 = new Date(this.current[1].getFullYear(), this.current[1] - (this.showNum - 1), 1)
                    var date2 = new Date(this.current[0].getFullYear(), this.current[0], 1)
                    this.showDate = date1 > date2 ? date1 : date2
                } else {
                    this.showDate = new Date(this.current[1])
                }
            } else if (this.current[0]) {
                this.showDate = new Date(this.current[0])
            } else if (this.compareDate(this.start, this.today) <= 0 && this.compareDate(this.end, this.today) >= 0) {
                this.showDate = new Date(this.today)
            } else if (this.compareDate(this.start, this.today) > 0) {
                this.showDate = new Date(this.start)
            } else {
                this.showDate = new Date(this.end)
            }
            this.showDate.setDate(1) // 日需要重置为1，否则左右切换会出问题
        },
        previous: function () {
            if (!this.disabledPrevious) {
                this.showDate.setMonth(this.showDate.getMonth() - 1)
                this.update()
            }
        },
        next: function () {
            if (!this.disabledPrevious) {
                this.showDate.setMonth(this.showDate.getMonth() + 1)
                this.update()
            }
        },
        update: function () {
            this.selected.length = 0
            var calendar = {
                year: this.showDate.getFullYear(),
                month: this.showDate.getMonth() + 1
            }
            var date = new Date(this.showDate.getFullYear(), this.showDate.getMonth(), 1)
            var len = (date.getDay() - this.firstDay + 7) % 7
            date.setDate(date.getDate() - len)
            var i
            var lineDates = []
            for (i = 0; i < len; i++) {
                if (this.showOtherMonth) {
                    lineDates.push(this.processDate(date, [0, 0, i], ['previous-month-day']))
                } else {
                    lineDates.push({
                        invalid: true,
                        classList: ['invalid-day']
                    })
                }
                date.setDate(date.getDate() + 1)
            }
            // 日历第一行数据
            for (i = len; i < 7; i++) {
                lineDates.push(this.processDate(date, [0, 0, i]))
                date.setDate(date.getDate() + 1)
            }
            var dates = [lineDates]
            var month = this.showDate.getMonth() // 当月日期
            for (i = 0; i < 5; i++) { // 公共6行，还剩5行
                lineDates = []
                for (var j = 0; j < 7; j++) {
                    if (date.getMonth() === month) {
                        lineDates.push(this.processDate(date, [0, i + 1, j]))
                    } else if (this.showOtherMonth) {
                        lineDates.push(this.processDate(date, [0, i + 1, j], ['next-month-day']))
                    } else {
                        lineDates.push({
                            invalid: true,
                            classList: ['invalid-day']
                        })
                    }
                    date.setDate(date.getDate() + 1)
                }
                dates.push(lineDates)
            }
            calendar.dates = dates
            this.calendars = [calendar]
        },
        processDate: function (date, position, classList) {
            var dateObj = {
                day: date.getDate(),  // 几日
                date: new Date(date), // 为了节省内存，这个属性应该去掉
                position: position
            }
            var dateName
            classList = classList || []
            if (this.compareDate(date, this.start) < 0 || this.compareDate(date, this.end) > 0) {
                dateObj.invalid = true
                classList.push('invalid-day')
            }
            switch (date.getDay()) {
            case 0:
                classList.push('sunday')
                break
            case 6:
                classList.push('saturday')
                break
            }
            if (this.compareDate(date, this.today) === 0) {
                dateObj.dateName = '今天'
                classList.push('today')
                if (this.mode === 'rangeFrom' && this.defaultToday && !this.current[0] && !dateObj.invalid) {
                    classList.push('from-day')
                    this.selected[0] = dateObj
                }
            } else {
                dateName = this.getFestival(date)
                if (dateName) {
                    dateObj.dateName = dateName
                    classList.push('festival')
                }
            }
            if (typeof this.buildContent === 'function') {
                this.buildContent(date, dateName)  // @TODO 需要返回html
            }
            var isGtFromDay = false
            if (this.current[0]) {
                if (this.compareDate(date, this.current[0]) > 0) {
                    isGtFromDay = true
                } else if (this.compareDate(date, this.current[0]) === 0) {
                    classList.push('from-day')
                    this.selected[0] = dateObj
                }
            }
            if (this.current[1]) {
                if (this.compareDate(date, this.current[1]) < 0) {
                    if (isGtFromDay) {
                        classList.push('range-day')
                    }
                } else if (this.compareDate(date, this.current[1]) === 0) {
                    classList.push('to-day')
                    this.selected[1] = dateObj
                }
            }
            dateObj.classList = classList
            return dateObj
        },
        getFestival: function (date) {
            var name = (date.getMonth() + 1) + '-' + date.getDate()
            var dateName = this.festivalMap[name]
            if (dateName) {
                return dateName
            }
            name = date.getFullYear() + '-' + name
            dateName = this.festivalStorage[dateName]
            return dateName
        },
        pick: function (date) {
            if (date.invalid) {
                return
            }
            this.date = this.dateToString(date.date)
            if (this.mode === 'rangeTo') {
                // 需要做区间处理
                if (this.current[0]) {
                    if (this.compareDate(date.date, this.current[0]) < 0) {
                        return
                    }
                    this.setRangeDate(this.selected[0] ? this.selected[0].position : [0, 0, -1], date.position, function (dateObj) {
                        if (dateObj.invalid) {
                            return
                        }
                        var index = dateObj.classList.indexOf('range-day')
                        if (index === -1) {
                            dateObj.classList.push('range-day')
                        }
                    }, [1, 0])
                    date.classList.push('to-day')
                    if (this.selected[1]) {
                        this.setRangeDate(date.position, this.selected[1].position, function (dateObj) {
                            this.deleteArrayValue(dateObj.classList, 'range-day')
                        })
                        this.deleteArrayValue(this.selected[1].classList, 'to-day')
                    } else if (this.current[1]) {
                        var maxDate = this.calendars[date.position[0]]
                        if (this.showOtherMonth) {
                            maxDate = maxDate.dates[maxDate.dates.length - 1]
                            maxDate = maxDate[maxDate.length - 1].date
                        } else {
                            maxDate = new Date(maxDate.year, maxDate.month, 0) // 当月最后一天
                        }
                        if (this.compareDate(this.current[1], maxDate) > 0) { // 这个判断是多余的
                            this.setRangeDate(date.position, [this.calendars.length - 1, 5, 7], function (dateObj) {
                                this.deleteArrayValue(dateObj.classList, 'range-day')
                            })
                        }
                    }
                } else if (this.selected[1]) {
                    this.deleteArrayValue(this.selected[1].classList, 'to-day')
                    date.classList.push('to-day')
                } else {
                    date.classList.push('to-day')
                }
                this.selected[1] = date
                this.current[1] = date.date
            } else {
                if (this.selected[0]) {  // 选中的日期对象存起来，不用每次都做处理
                    this.deleteArrayValue(this.selected[0].classList, 'from-day')
                }
                date.classList.push('from-day')
                this.selected[0] = date
                this.current[0] = date.date
            }
            this.$emit('input', this.mode==='rangeFrom'?this.current[0]:this.current[1]);
        },
        getDateObj: function (position) {
            var dateObj = this.calendars
            for (var i = 0, len = position.length; i < len; i++) {
                dateObj = dateObj[position[i]]
                if (i === 0) {
                    dateObj = dateObj.dates
                }
            }
            return dateObj
        },
        deleteArrayValue: function (arr, val) {
            if (!arr) {
                return
            }
            var index = arr.indexOf(val)
            if (index !== -1) {
                arr.splice(index, 1)
            }
        },
        setRangeDate: function (pos1, pos2, fn, rangeType) { // rangeType是一个数组，第1个值为0表示左闭区间，为1表示左开区间，第二个值为0表示右开区间，为1表示右闭区间
            rangeType = rangeType || [0, 0]  // 默认是左闭右开区间，与Math.random函数保持一致
            for (var i = pos1[0]; i <= pos2[0]; i++) {
                for (var j = (i === pos1[0] ? pos1[1] : 0), len2 = (i === pos2[0] ? pos2[1] : 5); j <= len2; j++) {
                    for (var k = (i === pos1[0] && j === pos1[1] ? pos1[2] + rangeType[0] : 0), len3 = (i === pos2[0] && j === pos2[1] ? pos2[2] + rangeType[1] : 7); k < len3; k++) {
                        fn.call(this, this.calendars[i].dates[j][k])
                    }
                }
            }
        },
        enter: function (date) {
            if (date.invalid) {
                return
            }
            if (this.mode === 'rangeTo') {
                if (this.current[0] && this.compareDate(date.date, this.current[0]) >= 0) {
                    this.setRangeDate(this.selected[0] ? this.selected[0].position : [0, 0, -1], date.position, function (dateObj) {
                        if (dateObj.invalid) {
                            return
                        }
                        var index = dateObj.classList.indexOf('range-day')
                        if (index === -1) {
                            dateObj.classList.push('range-day')
                        }
                    }, [1, 0])
                    date.classList.push('hover-to-day')
                    if (this.selected[1]) {
                        this.setRangeDate(date.position, this.selected[1].position, function (dateObj) {
                            this.deleteArrayValue(dateObj.classList, 'range-day')
                        })
                        this.deleteArrayValue(this.selected[1].classList, 'to-day')
                    } else if (this.current[1]) {
                        var maxDate = this.calendars[date.position[0]]
                        if (this.showOtherMonth) {
                            maxDate = maxDate.dates[maxDate.dates.length - 1]
                            maxDate = maxDate[maxDate.length - 1].date
                        } else {
                            maxDate = new Date(maxDate.year, maxDate.month, 1)
                            maxDate.setDate(maxDate.getDate() - 1)
                        }
                        if (this.compareDate(this.current[1], maxDate) > 0) {
                            this.setRangeDate(date.position, [this.calendars.length - 1, 5, 7], function (dateObj) {
                                this.deleteArrayValue(dateObj.classList, 'range-day')
                            })
                        }
                    }
                } else {
                    date.classList.push('hover-to-day')
                }
            } else {
                date.classList.push('hover-from-day')
            }
            if (typeof this.hoverIn === 'function') {
                this.hoverIn(date)
            }
        },
        out: function (date, e) {
            if (date.invalid) {
                return
            }
            if (this.mode === 'rangeTo') {
                if (this.current[0] && this.compareDate(date.date, this.current[0]) >= 0) {
                    var pos1 = this.selected[0] ? this.selected[0].position : [0, 0, -1]
                    if (this.selected[1]) {
                        this.setRangeDate(pos1, this.selected[1].position, function (dateObj) {
                            if (dateObj.invalid) {
                                return
                            }
                            var index = dateObj.classList.indexOf('range-day')
                            if (index === -1) {
                                dateObj.classList.push('range-day')
                            }
                        }, [1, 0])
                        if (this.selected[1].classList.indexOf('to-day') === -1) {
                            this.selected[1].classList.push('to-day')
                        }
                        this.setRangeDate(this.selected[1].position, date.position, function (dateObj) {
                            this.deleteArrayValue(dateObj.classList, 'range-day')
                        })
                    } else if (this.current[1]) {
                        var maxDate = this.calendars[date.position[0]]
                        if (this.showOtherMonth) {
                            maxDate = maxDate.dates[maxDate.dates.length - 1]
                            maxDate = maxDate[maxDate.length - 1].date
                        } else {
                            maxDate = new Date(maxDate.year, maxDate.month, 1)
                            maxDate.setDate(maxDate.getDate() - 1)
                        }
                        if (this.compareDate(this.current[1], maxDate) > 0) {
                            this.setRangeDate(pos1, [this.calendars.length - 1, 5, 6], function (dateObj) {
                                if (dateObj.invalid) {
                                    return
                                }
                                var index = dateObj.classList.indexOf('range-day')
                                if (index === -1) {
                                    dateObj.classList.push('range-day')
                                }
                            }, [1, 1])
                        } else {
                            this.setRangeDate(pos1, date.position, function (dateObj) {
                                this.deleteArrayValue(dateObj.classList, 'range-day')
                            }, [1, 0])
                        }
                    } else {
                        this.setRangeDate(pos1, date.position, function (dateObj) {
                            this.deleteArrayValue(dateObj.classList, 'range-day')
                        }, [1, 0])
                    }
                }
                this.deleteArrayValue(date.classList, 'hover-to-day')
            } else {
                this.deleteArrayValue(date.classList, 'hover-from-day')
            }
            if (typeof this.hoverOut === 'function') {
                this.hoverOut(date)
            }
        }
    },
    mounted: function () {
        var dayStrs = [{
            day: 0,
            str: '日'
        }, {
            day: 1,
            str: '一'
        }, {
            day: 2,
            str: '二'
        }, {
            day: 3,
            str: '三'
        }, {
            day: 4,
            str: '四'
        }, {
            day: 5,
            str: '五'
        }, {
            day: 6,
            str: '六'
        }]
        this.headers = dayStrs.slice(this.firstDay).concat(dayStrs.slice(0, this.firstDay))
        this.init()
        this.update()
    }
}
</script>


<style lang="less">
.calendar-panel {
    font: 13px/20px Arial,'Microsoft YaHei',sans-serif,SimSun;
    color: #666;
    background: #fff;
    box-shadow: 0 0 16px 2px rgba(0,0,0,0.2);
    > div {
        width: 100%;
        overflow-x: hidden;
        position: relative;
        padding-bottom: 10px;
    }
    &:after {
        content: "";
        display: table;
        clear: both;
    }
    .month-nav {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        > span {
            position: absolute;
            top: 6px;
            > i {
                display: block;
                height: 30px;
                width: 30px;
                background: url(http://js.40017.cn/cn/public/module/Calendar/0.3/calendar_arrow_white.png?v=2) 0 0 no-repeat;
                overflow: hidden;
                cursor: pointer;
            }
            > .previous-month {
                background-position: -8px 0px;
                &:hover {
                    background-position: -100px 0;
                }
            }
            > .next-month {
                background-position: -49px 0px;
                &:hover {
                    background-position: -143px 0px;
                }
            }
            .previous-month-disabled, .previous-month-disabled {
                cursor: default;
            }
            > .previous-month-disabled, > .calendar-panel.white:hover {
                background-position: -100px -43px;
            }
            > .next-month-disabled, > .next-month-disabled:hover {
                background-position: -142px -43px;
            }
        }
        > .previous-month-panel {
            left: 0;
            padding: 0 2px 0 6px;
        }
        > .next-month-panel {
            right: 0;
            padding: 0 6px 0 2px;
        }
    }
    .calendar-container {
        white-space: nowrap;
        position: relative;
        font-size: 0;
    }
    .calendar-wrapper {
        display: inline-block;
        vertical-align: top;
        font-size: 13px; /* 还原字号 */
        > h6 {
            margin: 0;
            padding: 0;
            height: 42px;
            text-align: center;
            > span {
                display: inline-block;
                margin-top: 10px;
                color: #333;
                font-size: 13px;
                line-height: 20px;
            }
        }
        > div {
            padding: 2px 8px 0;
        }
        table {
            margin: 0;
            padding: 0;
            border-spacing: 0;
            border-collapse: collapse;
            width: 100%;
        }
        tr {
            height: 28px
        }
        th, td {
            width: 30px;
            padding: 0;
            vertical-align: middle;
            text-align: center;
            border: none;
        }
        th {
            font-weight: normal;
        }
        td {
            cursor: pointer;
            .d {
                display: block;
            }
        }
        .today {
            color: #ffa63c;
        }
        .festival {
            color: #b54800;
        }
        .hover-from-day, .hover-to-day {
            background-color: #eee;
            color: #333;
        }
        .hover-day, .range-day {
            background:#a4e98d;
            color: #333;
        }
        .invalid-day {
            color: #ccc;
        }
        .from-day, .to-day {
            background: #50b400;
            color: #fff;
        }
    }
    > i, > i > em {
        position: absolute;
        height: 0;
        width: 0;
        line-height: 0;
        border-style: solid;
    }
    > i {
        border-color: #fff transparent;
        > em {
            border-color: #fff transparent;
            left: -8px;
        }
    }
    > .left-bottom, > .right-bottom {
        border-width: 0 8px 8px;
        top: -8px;
        > em {
            border-width: 0 8px 8px;
            top: 1px;
        }
    }
    > .left-bottom, > .left-top {
        left: 24px;
    }
    > .right-bottom, > .right-top {
        right: 24px;
    }
    > .left-top, > .right-top {
        border-width: 8px 8px 0;
        bottom: -8px;
        > em {
            border-width: 8px 8px 0;
            bottom: 1px;
        }
    }
}
.calendar-panel.large {
    font-size: 14px;
    .month-nav {
        > span {
            top: 10px;
        }
        > .previous-month-panel {
            padding: 0 2px 0 10px;
        }
        > .next-month-panel {
            padding: 0 10px 0 2px;
        }
    }
    .calendar-wrapper {
        font-size: 14px;
        > h6 {
            height: 45px;
            > span {
                font-size: 14px;
                margin-top: 15px;
                line-height: 22px;
            }
        }
        > div {
            padding: 10px 10px 0;
        }
        tr {
            height: 31px;
        }
    }
}
</style>



