<template lang="html">
    <li class="sf-menuh__group">
        <div class="sf-menuh__group-title">
            {{title}}
        </div>
        <ul class="sf-menuh sf-menuh--l2">
            <slot></slot>
        </ul>
    </li>
</template>

<script>
export default {
    name: 'menuh-group',
    data() {
        return {
            level: 0
        }
    },
    props: {
        title: {
            type: String,
            default: ''
        }
    },
    methods: {
        addLevel() {
            this.level++;
            this.$slots.default.forEach(item=>{
                if(!item.child) return;
                item.child.addLevel();
            })
        }
    }
}
</script>

<style lang="less">
    // group
    .sf-menuh__group {

    }
    .sf-menuh__group-title {
        color: #666;
        font-size: 0.75em;
        padding-left: 10px;
        padding-right: 15px;
    }
</style>



