<template lang="html">
    <div>
        <ul class="sf-crumbs">
            <li>布局技巧</li>
            <li>列表布局</li>
        </ul>
        <sf-panel-box class="sf-mv10 sf-mh15" title="固定列">
            <sf-table-shell :fix-left="1" :fix-right="1">
                <template scope="scope">
                    <table class="sf-table" >
                        <tr>
                            <th class="sf-tc sf-nowrap">序号</th>
                            <th>商户编号</th>
                            <th>注册号</th>
                            <th>商户名称</th>
                            <th>商户编号</th>
                            <th>注册号</th>
                            <th>商户名称</th>
                            <th>商户编号</th>
                            <th>注册号</th>
                            <th>商户名称</th>
                            <th>操作</th>
                        </tr>
                        <tr v-for="(item, index) in list">
                            <td class="sf-tc sf-w40" v-text="index"></td>
                            <td v-text="item.guid" class="sf-nowrap"></td>
                            <td v-text="item.date" class="sf-nowrap"></td>
                            <td v-text="item.name" class="sf-nowrap"></td>
                            <td v-text="item.guid" class="sf-nowrap"></td>
                            <td v-text="item.date" class="sf-nowrap"></td>
                            <td v-text="item.name" class="sf-nowrap"></td>
                            <td v-text="item.guid" class="sf-nowrap"></td>
                            <td v-text="item.date" class="sf-nowrap"></td>
                            <td v-text="item.name" class="sf-nowrap"></td>
                            <td class="sf-nowrap">
                                <button class="sf-button sf-primary-o sf-sm" @click="$router.push({path:'/form', query: {id:item.guid}})">编辑</button>
                                <button class="sf-button sf-danger-o sf-sm" @click="remove(item)">删除</button>
                            </td>
                        </tr>
                    </table>
                </template>
            </sf-table-shell>
        </sf-panel-box>
    </div>
</template>

<script>
    import axios from '../utils/axios'
    export default {
        name: 'ListLayout',
        data() {
            return {
                list: []
            }
        },
        async created() {
        var res = (await axios.get('http://10.100.158.183:1234/_list/guid-name-date')).data
        this.list = res.data;
    }
    }
</script>

<style lang="css">
</style>



