<template lang="html">
    <tree-node :leaf="!hasSub" :folded="!showSub" :actived="actived" @click.stop="$emit('click')">
        <span @click="toggle"><slot name="title">{{title}}</slot></span>
        <slot></slot>
    </tree-node>
</template>

<script>

import isArray from 'lodash/isArray'

export default {
    name: 'node',
    props: {
        title: {
            default: ''
        },
        actived: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            hasSub: false,
            showSub: false
        }
    },
    mounted() {
        if(this.$slots.default) this.hasSub=true;
    },
    methods: {
        toggle() {
            this.showSub = !this.showSub;
        },
        expand(all) {
            this.showSub = true;
            if(!all) return;
            this.$nextTick(()=>{
                this.$children.forEach(node=>{ node.expand(true) })
            })
        },
        shrink() {
            this.showSub = false;
        }
    },
    updated() {
        this.hasSub=!!this.$slots.default;
    }
}
</script>




