<script type="text/javascript">
/**
 * input 抽象类
 */

export default {
    props: {
        placeholder: {
            type: String,
            default: ''
        },
        readonly: {
            type: Boolean,
            default: false
        },
        disabled: {
            type: Boolean,
            default: false
        },
        value: {
            default: ''
        }
    },
    methods: {
        update(value){
            this.$emit('input', value);
        }
    }
}

</script>



