<script type="text/javascript">
    export default {
        name: 'condition-box',
        props: {
            height: {
                default: 39
            },
            foldable: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                collapsed: true,
                expanding: false,
                collapsing: false,
                // 是否换行
                wrap: false
            }
        },
        methods: {
            // 判断是否聚焦在当前组件中溢出部分
            isFocusOverflow() {
                // 获取焦点元素
                if(this.$el.contains(document.activeElement)) {
                    if(this.isElementInViewport(document.activeElement)) {
                        return false;
                    }else {
                        return true;
                    }
                }else {
                    return false;
                }
            },
            keydownHandler(event) {
                // 如果是tab键
                if(event.keyCode === 9) {
                    setTimeout(()=>{
                        if(this.isFocusOverflow()) {
                            this.collapsed = false;
                        }
                    })
                }
            },
            // 判断元素是否可见
            isElementInViewport (el) {
                if(el.offsetTop > el.parentElement.offsetHeight) {
                    return false;
                }else {
                    return true;
                }
            },
            // 判断是否换行
            judgeWrap() {
                if(this.$el.children[0].offsetHeight > 39) {
                    this.wrap = true;
                }else {
                    this.wrap = false;
                }
            }
        },
        mounted() {
            this.judgeWrap();
            window.addEventListener('keydown', this.keydownHandler)
            window.addEventListener('resize', this.judgeWrap)
        },
        beforeDestroy() {
            window.removeEventListener('keydown', this.keydownHandler)
            window.removeEventListener('resize', this.judgeWrap)
        },
        render(h) {
            return h('div', {
                class: ['sf-condition-box', this.expanding?'sf-condition-box--expanding':'', this.collapsing?'sf-condition-box--collapsing':''],
                style: this.foldable?{'max-height': this.collapsed?(this.height+'px'):'2500px'}:{}
            }, [
                this.$slots.default,
                h('div', {class: ['sf-condition-box__corner']}, [
                    this.$slots.corner,
                    h('span', {
                        class: 'sf-condition-box__toogle',
                        on: {
                            click: event=>{
                                this.collapsed = !this.collapsed;
                            }
                        }
                    }, (this.foldable&&this.wrap)?[this.collapsed?'展开':'收起']:[])
                ])
            ])
        },
        watch: {
            collapsed(val) {
                if(val) {
                    this.expanding = false;
                    this.collapsing = true;
                    setTimeout(()=>{
                        this.collapsing = false;
                    }, 2000)
                }else {
                    this.expanding = true;
                    this.collapsing = false;
                    setTimeout(()=>{
                        this.expanding = false;
                    }, 2000)
                }
            }
        }
    }
</script>



