<template lang="html">
    <!-- <span class="sf-validator-message">
        <slot></slot> <message v-show="status==='fail'" :message="message" ref="message"></message>
    </span> -->
    <tooltip manual :value="status==='fail'" class="sf-validator-message" warn-o :content="message" placement="right">
        <slot></slot>
    </tooltip>
</template>

<script>

import isString from 'lodash/isString'
import isRegExp from 'lodash/isRegExp'
import isFunction from 'lodash/isFunction'
import isObject from 'lodash/isObject'
import isArray from 'lodash/isArray'
import extend from 'lodash/extend'
import isEmpty from 'lodash/isEmpty'
import debounce from 'lodash/debounce'
import Tooltip from '../tooltip/tooltip.jsx'

import message from './message.vue'

export default {
    name: 'validator-message',
    props: {
        rules: {
            type: Object,
            default() {
                return {}
            }
        },
        value: {
            default: ''
        }
    },
    data() {
        return {
            // 校验信息
            message: '',
            // 校验器的状态 pristine: 还没有校验， pending:校验中， fail:校验失败， success：校验成功
            status: 'pristine'
        }
    },
    mounted() {
        // this.$refs.message.left = this.$el.offsetWidth;
        // window.addEventListener('resize', this.resizeHandler)
    },
    destroyed() {
        // window.removeEventListener('resize', this.resizeHandler)
    },
    computed: {
        cookedRules() {
            return this.formateRules(this.rules);
        },
        ruleRules() {
            return this.$validatorRules;
        }
    },
    watch: {
        value(val) {
            // 修改状态为校验中
            this.status = 'pending';
            // 进行校验
            this.validateLazy(this.value, this.cookedRules, ({pass, message})=>{
                if(this.status !== 'pending') return;
                if(pass) {
                    this.status = 'success';
                    this.message = '';
                }else {
                    this.status = 'fail';
                    this.message= message
                }
            });
        },
    },
    methods: {
        async valid() {
            var {pass, message} = await this.validate(this.value, this.cookedRules);
            if(pass) {
                this.status = 'success';
                this.message = '';
            }else {
                this.status = 'fail';
                this.message= message
            }
            return pass;
        },
        reset() {
            this.status = 'pristine';
        },
        // resizeHandler() {
        //     if(this.$el) {
        //         this.$refs.message.left = this.$el.offsetWidth;
        //     }
        // },
        // 校验规则函数的生产工厂
        ruleFnFactory (rule) {
            // 如果规则为字符串
            if(isString(rule)) {
               return async function(value) {
                   var passed = await $.ajax({
                       url: rule,
                       data: {
                           value: value
                       },
                       dataType: "json"
                   });
                   if(passed===true || passed==='true') {
                       return true;
                   }else {
                       return false;
                   }
               }
            }
            // 如果规则为正则
            if(isRegExp(rule)) {
                return function(value) {
                    return Promise.resolve(rule.test(value));
                }
            }
            // 如果规则为函数
            if(isFunction(rule)) {
                return function(value) {
                    return Promise.resolve(rule(value));
                }
            }
        },
        // 将校验规则的配置转化为复杂的规则对象
        formateRules (rules) {
            // 用来判断是否为正则表达式
            var isRgx = /^\/(.*)\/$/,
                isUrl = /^\[(.+)\]$/,
                isName = /^\w+$/;
            var rules_ = {required: null, others:[]};
                // 将规则名转化为规则
            for(var key in rules) {
                // 如果key是name，首先通过 name 获取 rule的实体,
                if(isName.test(key)) {
                    var rule = this.ruleRules[key];
                    if(key === 'required') {
                        rules_.required = {
                            name: key,
                            message: rules[key],
                            fn: this.ruleFnFactory(rule)
                        };
                    }else {
                        rules_.others.push({
                            name: key,
                            message: rules[key],
                            fn: this.ruleFnFactory(rule)
                        });
                    }
                }
                // 如果 key 是 Rgx 字符串，将 key 转化为正则，最终转化为校验方法
                if(isRgx.test(key)) {
                    var rule = new RegExp(key.replace(/^\/(.*)\/$/, '$1'));
                    rules_.others.push({
                        message: rules[key],
                        fn: this.ruleFnFactory(rule)
                    });
                }
                // 如果key是正则url，那么创建远程校验方法
                if(isUrl.test(key)) {
                    var rule = key.replace(isUrl,'$1');
                    rules_.others.push({
                        message: rules[key],
                        fn: this.ruleFnFactory(rule)
                    });
                }
            }
            return rules_;
        },
        validate: async function(value, rules) {
            // 首先处理特殊的required校验
            if(isObject(rules.required)) {
                // 如果含有required校验且校验没有通过，直接枪毙
                if(!this.ruleRules.required(value)) {
                    return {pass: false, message: rules.required.message};
                }
            }else {
                // 如果不含required校验且校验没有通过，直接通过
                if(!this.ruleRules.required(value)) {
                    return {pass: true, message: ''};
                }
            }
            for(var i=0,rule; rule=rules.others[i]; i++) {
                if(await rule.fn(value)){
                    // return {pass: true, message: ''};
                }else {
                    return {pass: false, message: rule.message};
                }
            }
            // 最有再校验一次required
            if(isObject(rules.required)) {
                if(this.ruleRules.required(value)) {
                    return {pass: true, message: rules.required.message};
                }
            }
            return {pass: true};
        },
        validateLazy:debounce(function(value, rules, next){
            this.validate(value, rules).then(r=>{
                next(r);
            })
        },300)
    },
    components: {
        message: message,
        Tooltip
    }
}
</script>


