export default {
    name: 'switch',
    props: {
        value: {
            type: Boolean,
            default: false
        },
        offText: {
            type: String,
            default: 'OFF'
        },
        onText: {
            type: String,
            default: 'ON'
        }
    },
    methods: {
        switch() {
            this.$emit('input', !this.value);
        }
    },
    render(h) {
        return <div class="sf-switch" {...{class: {"sf-switch--checked": this.value}}} onClick={this.switch}>
                <span class="sf-switch__label">
                    <span class="sf-switch__inner"  x-offtext={this.offText} x-ontext={this.onText} ></span>
                    <span class="sf-switch__switch"></span>
                </span>
            </div>
    }
}


