<template lang="html">
    <ul :class="['sf-menuh', 'sf-menuh--l'+level]">
        <slot></slot>
    </ul>
</template>

<script>
    export default {
        name: "menuh",
        data() {
            return {
                level: 0
            }
        },
        mounted() {
            this.addLevel();
        },
        methods: {
            addLevel() {
                this.level++;
                this.$slots.default.forEach(item=>{
                    if(!item.child) return;
                    item.child.addLevel();
                })
            }
        }
    }
</script>



