<template lang="html">
<div class="fs-row">
    <div
        class="fs-row__inner"
        :style="{
            'display': 'flex',
            'justify-content': justifyContent,
            'align-items': alignItems,
            'margin': '0 '+(-1*parseFloat(gutter)/2)+'px'
        }"

    >
        <slot></slot>
    </div>
</div>
</template>

<script>

import isString from 'lodash/isString'
import isNumber from 'lodash/isNumber'
import includes from 'lodash/includes'
import resize from '../../directives/resize'
var alignValues = ['left', 'right', 'center', 'justify']
var valignValues = ['top', 'bottom', 'center', 'stretch']
var align2JustifyContent= {
    left: 'flex-start',
    right: 'flex-end',
    center: 'center',
    justify: 'space-between'
}
var valign2alignItems= {
    top: 'flex-start',
    bottom: 'flex-end',
    center: 'center',
    stretch: 'stretch'
}

export default {
    name: 'row',
    props: {
        // gutter 只接受px
        gutter: {
            type: String,
            default: '0',
            validator: value=>{
                if(value === '0') return true;
                if(isString(value)) {
                    return /px$/.test(value);
                }
                return false;
            }
        },
        // 水平对齐方式
        align: {
            type: String,
            default: 'left',
            validator: value=>{
                return includes(alignValues, value);
            }
        },
        // 垂直对其方式
        valign: {
            type: String,
            default: 'top',
            validator: value=>{
                return includes(valignValues, value);
            }
        }
    },
    data: function () {
        return {
            width: 0,
            itemsWidth: []
        }
    },
    computed: {
        justifyContent() {
            return align2JustifyContent[this.align];
        },
        alignItems() {
            return valign2alignItems[this.valign];
        }
    },
    directives: {
        resize
    },
    methods: {
        updateWidth() {
            this.width = this.$el.offsetWidth;
        }
    }
}
</script>



