<template lang="html">
    <div>
        <ul class="sf-crumbs">
            <li>日报</li>
            <li>我的日报</li>
        </ul>
        <div class="sf-panel sf-pb15 sf-pt5 sf-mv10 sf-mh15" v-show="state === 'loaded'">
            <table class="sf-table">
                <tr>
                    <th class="sf-tc">序号</th>
                    <th>用户id</th>
                    <th>姓名</th>
                    <th>性别</th>
                    <th>地址</th>
                    <th>创建时间</th>
                </tr>
                <tr v-for="(item, index) in userInfo">
                    <td class="sf-tc sf-w40">{{index + 1}}</td>
                    <td>{{item.userId}}</td>
                    <td>{{item.userName}}</td>
                    <td>{{item.sex}}</td>
                    <td>{{item.address}}</td>
                    <td>{{item.createTime | moment('YYYY年MM月DD日')}}</td>
                </tr>
            </table>
            <sf-pagination :total="paging.totalCount" :page-size="paging.pageSize" class="sf-mt10" v-model="paging.pageIndex" @input="getInfoByPage"></sf-pagination>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'MyDaily',
        data() {
            return {
                userInfo: [],
                paging: {
                    pageIndex: 1, // 分页页码
                    totalCount: 0, // 总条数
                    pageSize: 5, // 每页的数据量
                    condition: {}
                },
                // init loaded loading
                state: 'init'
            }
        },
        created() {
            $.ajax({
                url: 'http://localhost:8011/milestone/admin/loadList',
                type: 'post',
                contentType: 'application/json',
                data: JSON.stringify(this.paging)
            }).then(function(res){
                this.userInfo = res.content;
                this.paging.totalCount = res.totalCount;
                this.state = 'loaded';
            }.bind(this));
        },
        methods: {
            getInfoByPage() {
                $.ajax({
                    url: 'http://localhost:8011/milestone/admin/list',
                    type: 'post',
                    contentType: 'application/json',
                    data: JSON.stringify(this.paging)
                }).then(function(res){
                    this.userInfo = res;
                }.bind(this));
            }
        }
    }
</script>

<style lang="css">

</style>


