<template lang="html">
    <div class="sf-option">
        <slot></slot>
    </div>
</template>
<script>
    export default {
        name: 'option',
        props:{
            // 节点值
            value: {
                default: undefined
            },
            // 显示指定 option 被选中时候，显示在 input 框中的内容
            // 当 slot 的内容不是文本的时候可以指定该属性
            title: {
                default: ''
            }
        },
        data() {
            return {
                // 是否被选中
                selected: false,
                // 是否待选中
                forSelected: false
            }
        },
        computed: {
            // o显示在 input 框中的内容，如果显示指定 title ，则取title ，否则取slot中text
            optionTitle() {
                return this.title || this.value;

            },
            // 实际取的value值
            optionValue() {
                // if(this.value === undefined) return this.$el.textContent || this.optionTitle || '';
                return this.value;
            }
        },
        mounted() {
            this.$parent.options.push(this);
        },
        beforeDestroy() {
            this.$parent.options.splice(this.$parent.options.indexOf(this), 1);
        }
    }
</script>



