<template lang="html">
    <span class="sf-checkbox"><input
            type="checkbox"
            class="sf-checkbox__input"
            v-model="model"
            :value="option"
        ><span class="sf-checkbox__fake"></span><slot></slot>
    </span>
</template>

<script>

    export default {
        name: 'checkbox',
        props:{
            option: '',
            value: ''
        },
        computed: {
            model: {
                set(val) {
                    this.$emit('input', val);
                },
                get() {
                    return this.value;
                }
            }
        }
    }
</script>



