<template lang="html">
    <div class="sf-panel-box">
        <div class="sf-panel-box__header">
            <span class="sf-panel-box__title">
                <slot name="header">{{title}}</slot>
            </span>
            <span class="sf-panel-box__tools">
                <slot name="tool"></slot>

                <i class="sf-panel-box__toggle fa" :class="[this.show?'fa-chevron-up':'fa-chevron-down']" @click="toggle"></i>
                <i class="sf-panel-box__close fa fa-times" v-if="closable" @click="$emit('close')"></i>
            </span>
        </div>
        <transition name="slide">
            <div class="sf-panel-box__body" v-show="show">
                <slot></slot>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
    name: 'panel-box',
    props: {
        collapsed: {
            type: Boolean,
            default: false
        },
        closable: {
            type: Boolean,
            default: false
        },
        title: {
            default: ''
        }
    },
    data() {
        return {
            show: true
        }
    },
    mounted() {
        this.show = !this.collapsed;
    },
    methods: {
        toggle() {
            this.show = !this.show;
        }
    }
}
</script>
<style lang="css">
    .sf-panel-box {
        background-color: white;
    }
    .sf-panel-box__header {
        background-color: gray;
        display: flex;
        justify-content: space-between;
    }
    .sf-panel-box__tools > i {
        cursor: pointer;
        margin-left: 5px;
    }
    .sf-panel-box__title {
        flex: 1;
    }
</style>



