<template lang="html">
    <div class="sf-validator-message__message" :style="style">
		<div class="sf-validator-message__arrow">
			<em></em>
			<span></span>
		</div>
        <span class="sf-validator-message__content">
            {{message}}
        </span>
    </div>
</template>

<script>
export default {
    props: {
        message: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            left: 0,
            top: 0
        }
    },
    computed: {
        style() {
            return {
                left: (this.left+10)+'px',
                top: this.top+'px'
            }
        }
    }
}
</script>




