<template lang="html">
    <div
        class="sf-confirm"
        v-show="activeConfirm !== null"
    >
        <div
            class="sf-confirm__content"
            :style="{'width': width+'px'}"
            v-for="confirm in confirms.concat([activeConfirm])"
            v-if="confirm !== null"
            v-show="activeConfirm == confirm"
        >
            <div class="sf-confirm__body">
                <div class="sf-confirm__message" v-html="confirm.message"></div>
            </div>
            <div class="sf-confirm__footer">
                <button type="button" secondary @click="_undo">取消</button>
                <button type="button" @click="_ensure">确定</button>
            </div>
        </div>
    </div>
</template>

<script>
    import isFunction from 'lodash/isFunction'
    export default {
        name:'confirm',
        data: function () {
            return {
                // 提示框的宽度
                width: 400,
                // 所有当前需要呈现的提示框
                confirms: [],
                // 目前显示的提示框
                activeConfirm: null
            }
        },
        created(){

        },
        props: {
            icon: {
                type: String,
                default: ''
            }
        },
        methods: {
            confirm({message, callback}){
                // 聚焦优化
                // setTimeout(()=>{
                //     this.$el.querySelector('.confirm-ensure').focus();
                // },1);
                return  new Promise((resolve)=>{
                    // 将换行符 \n 替换为 <br>
                    message = message.replace('\n', '<br>');
                    this.confirms.unshift({message, callback, resolve});
                    this._ask();
                });

            },
            _ensure() {
                this._close(true);
            },
            _undo() {
                this._close(false);
            },
            _close(isEnsured) {
                if(this.activeConfirm === null) return ;

                if(isFunction(this.activeConfirm.callback)) {
                    this.activeConfirm.callback(isEnsured);
                }
                this.activeConfirm.resolve(isEnsured);
                this.activeConfirm = null;
                this._ask();
            },
            _ask() {
                if(this.activeConfirm === null) {
                    if(this.confirms.length > 0) {
                        this.activeConfirm = this.confirms.pop();
                    }
                }
            }
        }
    }
</script>



