<template lang="html">
    <div class="sf-pagination" v-if="total>0">
        <ul class="sf-pagination__btns">
            <li
                class="sf-pagination__btn"
                :class="{'sf-pagination__btn--inactived': value===1}"
                @click="prev"
            >
                <i class="fa fa-chevron-left"></i>
            </li>
            <li
                @click="focus(1)"
                v-show="!isWindowInBorderLeft"
                class="sf-pagination__btn"
            >1</li>
            <li
                @click="prevWindow()"
                v-show="!isWindowInBorderLeft"
                class="sf-pagination__btn"
            >...</li>
            <li
                v-for="page in pageWindow"
                class="sf-pagination__btn"
                :class="{'sf-pagination__btn--actived': value===page}"
                @click="focus(page)"
            >{{page}}</li>
            <li
                v-show="!isWindowInBorderRight"
                class="sf-pagination__btn"
                @click="nextWindow()"
            >...</li>
            <li
                @click="focus(maxPage)"
                v-show="!isWindowInBorderRight"
                class="sf-pagination__btn"
            >{{maxPage}}</li>
            <li
                class="sf-pagination__btn"
                @click="next"
                :class="{'sf-pagination__btn--inactived': value===maxPage}"
            >
                <i class="fa fa-chevron-right"></i>
            </li>
        </ul>
    </div>
</template>

<script>
    import debounce from 'lodash/debounce'
    export default {
        name: 'pagination',
        props: {
            // 分页总条目, 当总条数为0 的时候不显示分页
            total: {
                type: Number,
                default: 0
            },
            // 当前的分页页码
            currentPage: {
                type: Number,
                default: 1
            },
            value: {
                type: Number,
                default: 1
            },
            // 每一页的分页量
            pageSize: {
                type: Number,
                default: 10
            },
            // 分页窗口的尺寸
            windowSize: {
                type: Number,
                default: 5
            }
        },
        created() {
        },
        computed: {
            // 最大的页数
            maxPage() {
                return  Math.ceil(this.total/this.pageSize);
            },
            // 分页的列表
            pageList() {
                var page = this.maxPage;
                var list = [];
                while(true) {
                    if(page<=0) break;
                    list.unshift(page);
                    page--;
                }
                return list;
            },
            // 窗口位于左边界
            isWindowInBorderLeft() {
                return this.value <= this.windowSize;
            },
            // 窗口位于右边界
            isWindowInBorderRight(){
                return Math.floor((this.maxPage - 1) / this.windowSize) === Math.floor((this.value - 1) / this.windowSize);
            },
            pageWindow() {
                var pageWindow = [];
                if(this.isWindowInBorderRight && this.isWindowInBorderLeft) {
                    for(var i = 1; i <= this.maxPage; i++) {
                        pageWindow.push(i);
                    }
                }else if(this.isWindowInBorderLeft) {
                    for(var i = 1; i <= this.windowSize; i++) {
                        pageWindow.push(i);
                    }
                }else if(this.isWindowInBorderRight) {
                    var windowStart = Math.floor((this.value - 1)/this.windowSize)*this.windowSize;
                    for(var i = windowStart+1; i <= this.maxPage; i++) {
                        pageWindow.push(i);
                    }
                }else {
                    var windowStart = Math.floor((this.value - 1)/this.windowSize)*this.windowSize;
                    for(var i = windowStart+1; i <= windowStart+this.windowSize; i++) {
                        pageWindow.push(i);
                    }
                }
                return pageWindow;
            }
        },
        data() {
            return {
                activedPage: this.currentPage,
                windowDir: 'left'
            }
        },
        methods: {
            prevWindow() {
                this.$emit('input', Math.floor((this.value - 1)/this.windowSize)*this.windowSize);
            },
            nextWindow() {
                this.$emit('input', Math.floor((this.value - 1)/this.windowSize+1)*this.windowSize+1);
            },
            prev() {
                if(this.value <= 1) return;
                this.$emit('input', this.value-1)
            },
            next() {
                if(this.value >= this.maxPage) return;
                this.$emit('input', this.value+1)
            },
            focus(page) {
                this.$emit('input', page);
            },
            // 重置分页
            reset() {
                this.$emit('input', 1);
            }
        }
    }
</script>

<style lang="less">
    .sf-pagination {
        display: flex;
        justify-content: flex-end;
        padding: 10px 0;
    }
    ul.sf-pagination__btns {
        margin: 0;
        padding: 0;
        list-style: none;
        display: flex;
    }
    li.sf-pagination__btn {
        user-select: none;
        color: #333;
        font-size: 12px;
        display: inline-block;
        padding: 0 0.75em;
        height: 2.5em;
        line-height: 2.5em;
        border: 1px solid #ccc;
        border-left: none;
        background-color: white;
        cursor: pointer;
    }

    li.sf-pagination__btn:hover {
        background-color: #dedede;
    }

    li.sf-pagination__btn:first-child {
        border-left: 1px solid #ccc;
    }

    li.sf-pagination__btn--actived {
        background-color: #666;
        border-color: #666;
        cursor: default;
        color: white;
        &:hover {
            background-color: #666;
        }
    }

    li.sf-pagination__btn--inactived {
        opacity: 0.4;
        cursor: not-allowed;
        &:hover {
            background-color: white;
        }
    }
</style>



