

<script>
import expand from '../expand/expand.vue'
import inputBox from '../input-box/input-box.vue'
import isArray from 'lodash/isArray'

export default {
    render(h) {
        return h('div', { class: ['sf-tree-select'] }, [
            h('expand', {
                props: { show: this.showExpand&&!this.readonly },
                ref: 'expand'
            }, [
                h('input-box', {
                    on: {
                        focus: event=>{ this.showExpand = true },
                        esc: event=>{ this.showExpand = false },
                        tab: event=>{
                            setTimeout(()=>{
                                if(!this.$refs.inputBox.$el.contains(document.activeElement)) {
                                    this.showExpand = false;
                                }
                            })
                        },
                        // input: val=>{
                        //     this.value = val;
                        // }
                    },
                    ref: 'inputBox',
                    props: {
                        editable: false,
                        value: this.searchText
                    }
                }, [
                    h('span', [this.title])
                ]),
                h('div', {
                    slot: 'content',
                    class: ['sf-tree-select__tree-wrap']
                }, [
                    h('sf-tree', this.data.map(node=>this.renderNode(h, node)))
                ])
            ])
        ])
    },
    name: 'tree-select',
    props: {
        value: {
            default: ''
        },
        readonly: {
            type: Boolean,
            default: false
        },
        searchable: {
            type: Boolean,
            default: false
        },
        data: {
            type: Array,
            default() {
                return []
            }
        },
        titleKey: {
            type: String,
            default: 'title'
        },
        valueKey: {
            type: String,
            default: 'value'
        },
        childrenKey: {
            type: String,
            default: 'children'
        },
        searchKey: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            showExpand: false,
            focused: false,
            searchText: '',
            title: ''
        }
    },
    computed: {
        searchResult() {
            // TODO: 搜索功能
        }
    },
    methods: {
        renderNode(h, nodeData) {
            var children = nodeData[this.childrenKey];
            if(this.value===nodeData[this.valueKey]) {
                this.title = nodeData[this.titleKey];
            }
            return h('sf-node', {
                attrs: {
                    actived: this.value===nodeData[this.valueKey]
                },
                on: {
                    click: event=>{
                        this.$emit('input', nodeData[this.valueKey]);
                    }
                }
            }, [
                h('span', {
                    slot: 'title'
                }, [
                    nodeData[this.titleKey]
                ]),
                isArray(children)?children.map(node=>this.renderNode(h, node)):''
            ])
        },
        otherClickHandler(event) {
            if(this.$el.contains(event.target)) {
                return ;
            }
            if(this.$refs.expand.$box.contains(event.target)) {
                return;
            }
            this.showExpand = false;
            this.searchText = '';
        }
    },
    mounted() {
        window.addEventListener('click', this.otherClickHandler)
    },
    destroyed() {
        window.removeEventListener('click', this.otherClickHandler)
    },
    components: {
        expand,
        'input-box': inputBox,
    }
}
</script>


