<template lang="html">

</template>

<script>

import isFunction from 'lodash/isFunction'

// v-model， 被选中的数据

export default {
    name: 'datatable-checkbox',
    props: {
        // 向外部传递数据
        value: {
            type: Array,
            default() {return []}
        },
        // 列的宽度
        width: {
            type: [Number, String],
            default: ''
        },
        condition: {
            type: Function,
            default() {
                return function(item) {
                    return true;
                }
            }
        },
        // 列的固定 可选 left right 默认 none
        fixed: {
            type: String,
            default: 'none'
        }
    },
    data() {
        return {
            // 单元格的类型，可选 index, checkbox, radio, normal
            type: 'checkbox',
            checkedItems: []
        }
    },
    watch: {
        checkedItems(val) {
            this.$emit('input', val);
        },
        value(val) {
            this.checkedItems = val;
        }
    }
}
</script>



