<template lang="html">
    <div>
        <ul class="sf-crumbs">
            <li>日报</li>
            <li><a href="#/daily-statis">日报统计</a></li>
            <li>用户详情</li>
        </ul>
        <sf-panel-box title="用户详情（单列水平布局居中对齐）" class="sf-mv20 sf-mh15">
            <dl dt-col="4">
                <dt>用户id</dt>
                <dd v-text="item.userId"></dd>
                <dt>姓名</dt>
                <dd v-text="item.userName"></dd>
                <dt>性别</dt>
                <dd v-text="item.sex"></dd>
                <dt>地址</dt>
                <dd v-text="item.address"></dd>
                <dt>创建时间</dt>
                <dd>{{item.createTime | moment}}</dd>
            </dl>
        </sf-panel-box>
        <sf-panel-box title="用户详情（单列水平布局左对齐）" class="sf-mv20 sf-mh15">
            <dl>
                <dt>用户id</dt>
                <dd v-text="item.userId"></dd>
                <dt>姓名</dt>
                <dd v-text="item.userName"></dd>
                <dt>性别</dt>
                <dd v-text="item.sex"></dd>
                <dt>地址</dt>
                <dd v-text="item.address"></dd>
                <dt>创建时间</dt>
                <dd>{{item.createTime | moment}}</dd>
            </dl>
        </sf-panel-box>
        <sf-panel-box title="用户详情（多列水平布局）" class="sf-mv20 sf-mh15">
            <dl class="row">
                <div class="col-4">
                    <dt>用户id</dt>
                    <dd v-text="item.userId"></dd>
                    <dt>姓名</dt>
                    <dd v-text="item.userName"></dd>
                </div>
                <div class="col-4">
                    <dt>性别</dt>
                    <dd v-text="item.sex"></dd>
                    <dt>地址</dt>
                    <dd v-text="item.address"></dd>
                </div>
                <div class="col-4">
                    <dt>创建时间</dt>
                    <dd>{{item.createTime | moment}}</dd>
                </div>
            </dl>
        </sf-panel-box>
        <sf-panel-box title="用户详情（单列垂直布局）" class="sf-mv20 sf-mh15">
            <dl vertical>
                <dt>用户id</dt>
                <dd v-text="item.userId"></dd>
                <dt>姓名</dt>
                <dd v-text="item.userName"></dd>
                <dt>性别</dt>
                <dd v-text="item.sex"></dd>
                <dt>地址</dt>
                <dd v-text="item.address"></dd>
                <dt>创建时间</dt>
                <dd>{{item.createTime | moment}}</dd>
            </dl>
        </sf-panel-box>
        <sf-panel-box title="用户详情（多列垂直布局）" class="sf-mv20 sf-mh15">
            <dl class="row" vertical>
                <div class="col-4">
                    <dt>用户id</dt>
                    <dd v-text="item.userId"></dd>
                    <dt>姓名</dt>
                    <dd v-text="item.userName"></dd>
                </div>
                <div class="col-4">
                    <dt>性别</dt>
                    <dd v-text="item.sex"></dd>
                    <dt>地址</dt>
                    <dd v-text="item.address"></dd>
                </div>
                <div class="col-4">
                    <dt>创建时间</dt>
                    <dd>{{item.createTime | moment}}</dd>
                </div>
            </dl>
        </sf-panel-box>
        <p class="sf-tc sf-mt40">
            <button type="button" class="sf-button" onclick="history.go(-1)">返回</button>
        </p>
    </div>
</template>

<script>
    import axios from '../../utils/axios'
    export default {
        name: 'Detail',
        data() {
            return {
                item: {}
            }
        },
        computed: {
            recId() {
                return this.$route.query.id;
            }
        },
        async created() {
            $.ajax({
                url: 'http://localhost:8011/milestone/admin/userDetail',
                type: 'post',
                data: {recId: this.recId}
            }).then(function(res){
                if (res.code == 0) {
                    this.item = res.data;
                } else {
                    this.$toast('查看详情失败', 'danger');
                }
            }.bind(this));
        }
    }
</script>

<style lang="css">
    
</style>



