<template>
    <div id="app" class="sf-page" :class="{'sf-collapsed': menuCollapse}">
        <div class="sf-page-left">
            <h1 class="sf-page-header"><i class="fa fa-cube sf-mr10" style="font-size: 1.3em"></i> <span v-show="!menuCollapse"><a href="#">最强里程碑</a></span></h1>
            <sf-menuv :mode="menuCollapse?'collapse':'normal'">
                <sf-menuv-item v-for="item in menu" @click="clickItem(item.href)" :actived="activedUrl===item.href" :key="item.title">
                    <i :class="['fa','fa-'+item.icon]" slot="icon"></i> <span v-text="item.title"></span>
                    <sf-menuv slot="submenu" v-if="item.children">
                        <sf-menuv-item v-for="subitem in item.children" @click="clickItem(subitem.href)" :actived="activedUrl===subitem.href" :key="subitem.title">
                            <span v-text="subitem.title"></span>
                            <div v-if="subitem.notice" class="sf-badge sf-fr" v-text="subitem.notice"></div>
                        </sf-menuv-item>
                    </sf-menuv>
                </sf-menuv-item>
            </sf-menuv>
        </div>
        <div class="sf-page-right">
            <div class="sf-page-topbar">
            <span>
               <i class="fa fa-bars sf-page-menu-toggle" @click="toggleMenu()"></i>
               <span style="color: #999;font-size:1.1em;margin: 0 1em;">|</span>
            <input type="text" class="sf-page-search" placeholder="搜索...">
            </span>
                <sf-menuh>
                    <sf-menuh-item>
                        <i class="fa fa-volume-up"></i>
                    </sf-menuh-item>
                    <sf-menuh-item>
                        <i class="fa fa-map"></i>
                    </sf-menuh-item>
                    <sf-menuh-item>
                        <i class="fa fa-user-o"></i> 您好，{{user.department}} {{user.username}}
                        <sf-menuh slot="submenu">
                            <sf-menuh-item><a href="http://authority.qas.17usoft.com/Account/Changepassword" target="_blank">修改密码</a></sf-menuh-item>
                            <sf-menuh-item><a href="/milestone/oauth/logout">退出</a></sf-menuh-item>
                        </sf-menuh>
                    </sf-menuh-item>
                </sf-menuh>
            </div>
            <keep-alive :include="/keep-alive/">
                <router-view></router-view>
            </keep-alive>

        </div>
        <div class="sf-page-footer">copyright © 2002-2017 同程网络科技股份有限公司 版权所有</div>
    </div>
</template>

<script>
    import menu from './router/menu.js'

    export default {
        name: 'app',
        data() {
            return {
                activedUrl: '',
                menuCollapse: false,
                menu,
                user: '',
            };
        },
        mounted() {
            this.activedUrl = location.hash.replace(/^#/, '');
            window.addEventListener('hashchange', event => {
                this.activedUrl = location.hash.replace(/^#/, '');
            // 页面切换的时候滚动到顶部
            window.scrollTo(0, 0);
            })
        },
        methods: {
            clickItem(url) {
                if (!url) return;
                this.activedUrl = url;
                location.hash = url;
            },
            toggleMenu() {
                this.menuCollapse = !this.menuCollapse;
            },
            submit() {
                this.$refs.validator.forceValid();
            },
        },
        created() {
            $.ajax({
                url: 'http://localhost:8011/milestone/admin/userInfo',
                type: 'get',
                dataType: 'jsonp',
                xhrFields: {
                    withCredentials: true
                },
                crossDomain: true,
                jsonpCallback: 'callback'
            }).then(function(res){
                this.user = res;
            }.bind(this));
        }
    }
</script>
<style media="screen" lang="less">
    @import '../node_modules/sailfish/src/sailfish.vue.less';
    @import '../node_modules/font-awesome/css/font-awesome.css';
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s
    }

    .fade-enter, .fade-leave-to {
        opacity: 0
    }
    h1 span a:hover {
        text-decoration: none;
    }
    .sf-theme-green .sf-page-footer {
        background-color: #9E9E9E;
        color: #FFF;
    }
</style>



