<template lang="html">
    <div class="sf-tab-panel" v-show="show">
        <slot></slot>
    </div>
</template>
<script>
    export default {
        name: 'tab-panel',
        props: {
            title: ''
        },
        data() {
            return {
                show: false
            }
        }
    }
</script>



